<template>
  <div>
    <ul>
      <li
        v-for="(val,key) in getList"
        :key="key"
      >
        <p v-if="typeof val!='object'">{{ val }}</p>
        <p v-else>類別: {{ key }}</p>
        <ul v-if="typeof val=='object'">
          <li
            v-for="(val,key) in val"
            :key="key"
            class="a"
          >
            <a
              :href="val.url"
              target="_blank"
            >{{ val.content }}</a>
            <a
              :href="val.urltwo"
              target="_blank"
            >{{ val.urltwotext }}</a><span>{{ val.installation }}</span>&nbsp;
            <span>標籤: {{ val.label }}</span><br>
            <p>使用情境: {{ val.situation }}</p><br>
            <p>工具說明: {{ val.contentText }}</p>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "componentTool",
    props: {
      getList: {
        type: Object
      }
    }
  };
</script>

<style lang="scss" scoped>
  @import "../../style/common.scss";
  @import "../../style/style.scss";
  .a {
    border: 1px solid $color3;
    padding: 0.3rem;
  }
  .a a:nth-of-type(1) {
    padding-right: 1rem;
  }
  .a p {
    background-color: blanchedalmond;
    display: inline-block;
  }
  .a p:nth-of-type(1) {
    margin-top: 1rem;
  }
</style>
