<template>
  <div id="child">
    <h3>Child</h3>
    <p>...</p>
    <input type="text">
  </div>
</template>

<script>
  export default {
    name: "child",
    props: ["method"],
    data() {
      return {
        val: "123",
        message: "jkjsldksjlds"
      };
    },
    methods: {
      click() {
        this.method(this.val);
      }
    },
    created() {
      this.$emit("created");
    },
    mounted() {
      this.$emit("send-message", self.message);
    }
  };
</script>

<style>
  #child {
    background-color: rgb(160, 193, 255);
    border: 1px solid #e111;
  }
</style>
