					<div id="tag-establish-a-smarty-environment"><h3>建立Smarty環境</h3></div>
					<div>
						<h4 id="tag-understanding-the-mvc-model"><ins>01:認識MVC模型</ins></h4>	
						<p>
							<b>MVC分別表示</b>
							<table class="table-style">
								<tr>
									<td></td>
									<td>簡易說明</td>
									<td>詳細說明</td>
								</tr>
								<tr>
									<td>M</td>
									<td>php控制db</td>
									<td>透過Model就能夠統一定義資料結構、存取操作。</td>
								</tr>
								<tr>
									<td>V</td>
									<td>html、css</td>
									<td>php負責把處理好的資料餵給Smarty樣板吃，然後去做網頁資料呈現。</td>
								</tr>
								<tr>
									<td>C</td>
									<td>php</td>
									<td>處理流程還有把View跟Model連結起來，例如資料新增成功(Modle)之後要呈現什麼畫面(View)</td>
								</tr>
							</table>
						</p>	
						<p>
							<b>MVC優缺點</b>
							<ul>
								<li>優點
									<ul>
										<li>可以幫助開發者更好地分離程式邏輯與頁面顯示。</li>
										<li>經過MVC資料分類後，較容易管理、修改資料。</li>
									</ul>
								</li>
								<li>缺點
									<ul>
										<li>MVC的缺點是由於他沒有明確的定義，所以完全理解MVC並不容易。使用MVC需要精心的規劃，由於它內部原理比較複雜，所以需要花費一些時間去思考。</li>
										<li>MVC並不適合小型甚至中等的開發程序，花費大量時間將MVC應用到規模並不大的開發程序通常會得不償失。
										</li>
									</ul>
								</li>
							</ul>
						</p>
						<p><b>建立MVC目錄環境</b>
							<table class="table-style" style="overflow:auto">
								<tr>
									<td>project</td>
									<td></td>
									<td></td>
									<td></td>
									<td>[目錄]<br>專案資料夾</td>
								<tr>
									<td></td>
									<td>common</td>
									<td></td>
									<td></td>
									<td>[目錄]<br>儲存常用的連結檔案。存放常用的.php與config</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>smarty_config.php</td>
									<td></td>
									<td>[.php]<br>自行建立透過smarty使controller連結viewer的檔案</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>common_const.php</td>
									<td></td>
									<td>[.php]<br>基本專案資料夾連結位置</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>direct.php</td>
									<td></td>
									<td>[.php]<br>轉址檔案</td>
								</tr>
								<tr>
									<td></td>
									<td>model</td>
									<td></td>
									<td></td>
									<td>[目錄]<br>存放所有操作資料庫的.php檔案</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>smarty</td>
									<td></td>
									<td>[目錄]<br>存放smarty執行檔</td>
								</tr>								
								<tr>
									<td></td>
									<td></td>
									<td></td>
									<td>Smarty.class.php</td>
									<td>[.php]<br>使用smarty內部程式碼</td>
								</tr>
								<tr>
									<td></td>
									<td>viewer</td>
									<td></td>
									<td></td>
									<td>[目錄]<br>存放所有前端程式碼</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>cache</td>
									<td></td>
									<td>[目錄]<br>儲存暫存檔案</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>configs</td>
									<td></td>
									<td>[目錄]<br>儲存前端自行定義的預設檔案.conf<br>編寫名稱與連結</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td>templates</td>
									<td></td>
									<td>[目錄]<br>存放樣板檔案</td>
								</tr>								
								<tr>
									<td></td>
									<td></td>
									<td></td>
									<td>css</td>
									<td>[目錄]<br>存放所有樣式檔案.css</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td></td>
									<td>js</td>
									<td>[目錄]<br>存放所有程式碼.js</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td></td>
									<td>images</td>
									<td>[目錄]<br>存放所有圖片.png</td>
								</tr>
								<tr>
									<td></td>
									<td></td>
									<td></td>
									<td>page...</td>
									<td>[目錄]<br>存放所有樣板檔案.html&nbsp;.tpl</td>
								</tr>
								<tr>
									<td></td>									
									<td></td>
									<td>compiles</td>
									<td></td>
									<td>[目錄]<br>存放自動產生的編譯檔案</td>
								</tr>
								<tr>
									<td></td>
									<td>controller</td>
									<td></td>
									<td></td>
									<td>[目錄]<br>存放所有連結viewerer的編輯檔.php</td>
								</tr>
								<tr>
									<td></td>
									<td></td>			
									<td>page...</td>
									<td></td>
									<td>[目錄]<br>同viewerer中的page...方便管理網站檔案資料</td>
								</tr>
							</table>
						</p>
					</div>
					<div>
						<h4 id="tag-establishing-a-smarty-environment"><ins>02:建立Smarty環境</ins></h4>
						<div>官方網站<a href="https://www.smarty.net/docs/zh_CN/" target="_blank">web</a><br>
							下載檔案<a href="modularization.html#tag-homemade-smarty-installation-package">Link</a>
							<ul>
								<li>1.先製作Smarty樣板配置</li>
								<li>2.建立Smarty所需四個資料夾</li>	
								<li>3.建立demo1.php檔，並載入smarty_config.php</li>
								<li>4.建立index.tpl樣板檔，且放置templates資料夾</li>
								<li>5.示範範例</li>
							</ul>
							<b>1.先製作Smarty樣板配置</b><br>
							<div class="divcode">
								新增一個檔案名為 smarty_config.php <br>
								<p>
									#project>controller>smarty_config.php <br><br>

									<span>//建立時要注意資料夾建立的位置</span><br>
									define('ROOTPATH',$_SERVER['DOCUMENT_ROOT']);<br>
									define('MODEL','/model');<br>
									define('viewer','/viewer/');<br>
									define('CONTROLLER','/controller');<br>
									define('SMARTY','/smarty/');<br><br>
										
									<span>//載入 smarty 類別檔</span><br>
									require_once(ROOTPATH.MODEL.SMARTY.'Smarty.class.php');<br><br>

										
									<span>//建立 smarty 物件</span><br>
									$sm = new Smarty;<br><br>

									<span>//配置 smarty 所需用的資料匣</span><br>
									$sm->templates_dir = ROOTPATH.viewer.'templates';<br>
									$sm->compile_dir = ROOTPATH.viewer.'templates_c';<br>
									$sm->config_dir = ROOTPATH.viewer.'configs';<br>
									$sm->cache_dir = ROOTPATH.viewer.'cache';<br><br>

									<span>//自訂 smarty 標識符號</span><br>
									$sm->left_delimiter = '{{ ';<br>
									$sm->right_delimiter = ' }}';<br><br>

									<span>//關閉暫存</span><br>
									$sm->caching = false;<br><br>

									<span>//閉閉測試</span><br>
									$sm->debugging = false;
								</p>
							</div>
						</div>
						<div>
							<b>2.建立Smarty所需四個資料夾</b><br>			
							
							<div class="divcode">
								建立一個viewer目錄，並在下方建立四個資料夾<br>
								<p>
									#project>viewer			
								</p>
							</div>							
							<table class="table-style">
								<tr>
									<td>資料夾</td>
									<td>用途</td>
								</tr>
								<tr>
									<td>cache</td>
									<td>做暫存用</td>
								</tr>
								<tr>
									<td>configs</td>
									<td>可放資料，或不放資料</td>
								</tr>
								<tr>
									<td>templates</td>
									<td>放樣板檔案</td>
								</tr>
								<tr>
									<td>templates_c</td>
									<td>放編譯檔案。
										當執行過樣板檔案時，會在此資料夾自動產生編譯後的檔案，
										所以不需要動用此資料夾資料。</td>
								</tr>
							</table>
						</div>	
						<div>	
							<b>3.建立demo1.php檔，並載入smarty_config.php</b>	<br>			
							<div class="divcode">
								<p>									
									require_once('smarty_config.php');<span>//載入 smarty 設定檔</span><br><br>
									$sm->assign('title',' 我所建立的第一個樣板內容~ ');<span>//$sm->assign()，寫入要傳遞的配置變量。也就是要給樣板的文字、數值、陣列內容是什麼</span><br><br>									
									$sm->display('index.tpl');<span>//$sm->display()，設定樣版連結。demo1.php此檔案，要套用的樣板是哪一個</span><br>
								</p>
							</div>						
						</div>
						<div>
							<b>4.建立index.tpl樣板檔，且放置templates資料夾</b><br>
							<div class="divcode">
								<p>
								#project>viewer>templates>index.tpl <br><br>
								</p>
								檔案內寫入html、css與引用符號等內容。
							</div>							
							
						</div>
						<div><b>5.示範範例</b><br>
							<div class="divcode">
								#demo1.php <br>
								<p>
								require_once('smarty_config.php'); <span>//載入 smarty 設定檔</span><br><br>
								$sm->assign('title',' 我所建立的第一個樣板內容~ '); <br><span>//$sm->assign('配置變量','內容')</span><br><span>//自訂配置變量與內容，透過配置變量傳遞內容到樣板中</span><br><br>
								$sm->display('index.tpl'); <br><span>//設定demo1.php的所有配置變量，傳遞到index.tpl樣板中</span>
								</p><br><br>
								#index.tpl <br>
								<p>
								&lt;!DOCTYPE html><br>
								&lt;html><br>
								&lt;head><br>
								&nbsp;&nbsp;&lt;meta charset="UTF-8"><br>
								&nbsp;&nbsp;&lt;title>{{ $title }}&lt;/title><br>
								<span>//要使用來自controller目錄內.php檔案的配置變量，需加上$符號</span><br>
								<span>//如此index.tpl的網站標題就會呈現(我所建立的第一個樣板內容~)的字樣</span>
								&lt;/head><br>
								&lt;body><br><br>
									
								&lt;/body><br>
								&lt;/html><br>
								</p>
							</div>

							
							

						</div>
					</div>
					<div>
						<h4 id="tag-document-description"><ins>03:文件說明</ins></h4>
						<div>
							<table class="table-style">
								<tr>
									<td>檔名</td>
									<td>說明</td>
									<td>程式碼</td>
								</tr>
								<tr>
									<td>smarty_config.php</td>
									<td>可以做一個常數的.php檔案</td>
									<td><div class="divcode">
                    <p>
										//載入自訂共用的常數檔<br>
										require_once($_SERVER['DOCUMENT_ROOT'].'/common/common_const.php');<br><br>
											
										//載入 Smarty Class<br>
										require_once(ROOT.MODEL.'smarty/Smarty.class.php');<br><br>
											
										//建立 Smarty 物件<br>
										$sm = new Smarty;<br><br>
											
										//配置 Smarty 物件的屬性<br>
										$sm->setTemplateDir(ROOT.VIEWER.'templates/')<br>
											->setCompileDir(ROOT.VIEWER.'compiles/')<br>
											->setConfigDir(ROOT.VIEWER.'configs/')<br>
											->setCacheDir(ROOT.VIEWER.'cache/');<br>
										$sm->left_delimiter = '<span>{'(實際情況無中間這個分隔號){'</span>   <br>
										$sm->right_delimiter = ' }}';<br><br>
										
										//關閉 cache<br>
										$sm->caching = false;<br><br>
											
										//關閉 debug<br>
										$sm->debugging = false;
                    </p>
                    </div>
									</td>
								</tr>
								<tr>
									<td>common_const.php</td>
									<td>對於所有資料夾位置，做預設</td>
									<td>
										<div class="divcode"><p>
											define('ROOT',$_SERVER['DOCUMENT_ROOT'].'/');<br>
											define('COMMON','common/');<br>
											define('CONTROLLER','controller/');<br>
											define('MODEL','model/');<br>
											define('VIEWER','viewer/');
										</p></div>
									</td>
								</tr>
								<tr>
									<td>direct.php</td>
									<td>使用header()這個函數達成首頁轉址的方式。header()要寫在檔案最前面。</td>
									<td>
										<div class="divcode"><p>
											//header(絕對網址)<br>
											header(Location: http://customer/controller/index.php)</p></div>
									</td>
								</tr>
								<tr>
									<td>controller內的.php</td>
									<td>之後controller內的.php檔案連結預設檔案</td>
									<td><div class="divcode"><p>
										//載入自訂共用的常數檔<br>
										require_once($_SERVER['DOCUMENT_ROOT'].'/common/common_const.php');<br><br>

										//載入 Smarty 配置檔<br>
										require_once(ROOT.COMMON.'smarty_config.php');
									</p></div></td>
								</tr>
							</table>
						</div>
					</div>
				</section>
				<section>
					<div id="tag-smarty-links-to-the-outside"><h3>Smarty連結外部</h3></div>
					<div>
						<h4 id="tag-understanding-function"><ins>01:認識函式</ins></h4>
						<div>
							<div class="divcode"><p>
								require() <span>//載入錯誤會停止</span><br>
								require_once() <br><span>//只載入一次(會自動判定暫存檔是否有檔案，有，不再載入)</span><br>
								include() <span>//載入有錯誤會繼續執行</span> 

							</p></div>
						</div>
					</div>
					<div>
						<h4 id="tag-css"><ins>02:CSS</ins></h4>					
						<div>viewer templates中html外部連結css<br>
							<div class="divcode">
								#project>viewer>templates>css>style.css <br>
								<p>
									body.style1{background-color:lightblue;}<br>
									body.style2{background-color:pink;}<br>
								</p><br>
								#project>controller>demo1.php <br>
								<p>
									require_once('smarty_config.php');<br>
									$sm->assign('style','style1'); <span>//連結css樣式中style1樣式</span><br>
									$sm->display('index.tpl');	<br><br>
								</p><br>
								#project>viewer>templates>page>index.tpl <br>
								<p>
									&lt;!DOCTYPE html><br>
									&lt;html><br>
									&lt;head><br>
									&nbsp;&nbsp;&lt;meta charset="UTF-8"><br>
									&nbsp;&nbsp;&lt;title>{{ $title }}&lt;/title><br>
									&nbsp;&nbsp;&lt;link rel="stylesheet" href="../css/style.css"><br>
									&lt;/head><br>
									&lt;body class="{{ $style }}"><br>
									&nbsp;&nbsp;{{ $title }}<br>
									&nbsp;&nbsp;{{ $contant }}<br>
									&lt;/body><br>
									&lt;/html><br>
								</p><br>
								//如此，只要在demo1.php的檔案上修改style的樣式，就可以變動index.tpl的背景顏色。
							</div>								
						</div>
					</div>
				</section>
				<section>
					<div id="tag-smarty-reference-content"><h3>Smarty引用內容</h3>
					<p>引用內容皆以實作案例為主</p>
					</div>
					<div>
						<h4 id="tag-array"><ins>01:陣列</ins>
						<a href="#" target="_blank">web</a>
						</h4>					
						<p>
							內容1
						</p>
					</div>
					<div>
						<h4 id="tag-modifier"><ins>02:修飾符 demo3.php</ins><a href="https://www.smarty.net/docs/zh_CN/language.modifiers.tpl" target="_blank">web</a></h4>
					</div>
				</section>
        <section>
          
        </section>
    </section>
  </div>
</template>

<script>
  export default {
    name: "smart"
  };
