//base
import Vue from 'vue'
import Vuetify from 'vuetify'
import MuseUI from 'muse-ui'

//data
import router from './router.js' //使用 vue-router
import App from './App'
import Helpers from 'muse-ui/lib/Helpers'
//import
import '@babel/polyfill' //配合 vuetify <icon>
import 'vuetify/dist/vuetify.min.css' //使用 vuetufy 的 css 樣式
import 'muse-ui/dist/muse-ui.css' //使用 muse-ui 的 css 樣式
import '../style/pretty.scss' //引用自訂程式碼塊 scss
import '../style/common.scss' //引用自訂通用 scss
import '../style/style.scss' //引用自訂 scss

Vue.use(MuseUI)
Vue.use(Helpers)
Vue.use(Vuetify)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
