<template>
  <div class="viewWidth">
    <div class="center">
      <div class="title">
        <h1>Muse-ui</h1>
        <p>框架樣式</p>
        <a href=""></a>
      </div>
      <div class="directory">
        <h2>目錄</h2>
        <ul>
          <li>Muse-ui 有哪些組件?</li>
          <li>Muse-ui 提供的工具?
            <ul>
              <li>Transitions 過度動畫?</li>
              <li>Directives 指令?</li>
              <li>Ripple 波紋組件?</li>
              <li>為何不能使用 muse-ui Ripple ?</li>
            </ul>
          </li>
          <li>Muse-ui 常用的 Grid 網格?</li>
          <li>Muse-ui 常用的 layout 布局?</li>
          <li>Muse-ui 使用色彩與Icon?</li>
          <li>Muse-ui 環境設定?</li>
        </ul>
        <hr>
      </div>
      <!-- li>h3+(ul>li>pre.prettyprint) -->
      <ul>

        <mu-expansion-panel zDepth="0">
          <div slot="header">
            <h3>Muse-ui 有哪些組件?</h3><a
              href="https://muse-ui.org/#/zh-CN/alert"
              target="_blank"
            >組件</a>
          </div>
          <table>
            <tr>
              <td>組件</td>
              <td>說明</td>
            </tr>
            <tr>
              <td>Alert</td>
              <td>mu-alert 組件來向用戶傳達訊息。<br>
                提示框/icon提示框/動畫提示框 <mark>message:傳遞訊息</mark>
              </td>
            </tr>
            <tr>
              <td>AppBar</td>
              <td>mu-appbar 組件作為頁面標題、logo、菜單和操作按鈕的容器。<br><mark>必定使用，頂部導覽列</mark>
              </td>
            </tr>
            <tr>
              <td>Avatar</td>
              <td>mu-avatar 用於顯示用戶頭向、簡短的問題和圖示<br>
                頭像/顯示文字或圖示
              </td>
            </tr>
            <tr>
              <td>Badge</td>
              <td>mu-badge 可以配合圖示、按鈕、列表等組件，用於標記一些數字和文字<br>
                徽章/像是Line 來幾則的紅色數字樣式
              </td>
            </tr>
            <tr>
              <td>Bottom Navigation</td>
              <td>mu-bottom-navigation 用於底部的導覽組件<br>
                <mark>手機適用，底部導覽</mark>
              </td>
            </tr>
            <tr>
              <td>Bottom Sheet</td>
              <td>mu-bottom-sheet
                底部表單是一個從屏幕底部邊緣向上滑出的一個面板，使用這種方式向用戶呈現一組功能。<mark>手機適用，底部導覽</mark>
              </td>
            </tr>
            <tr>
              <td>Breadcrumbs</td>
              <td>mu-breadcrumbs 組件是一個頁面輔助導覽器<br>
                麵包屑導覽
              </td>
            </tr>
            <tr>
              <td>Buttons</td>
              <td>mu-button 按鈕組件，可以制定不同顏色或圖示來替換標準的 html 按鈕</td>
            </tr>
            <tr>
              <td>Card</td>
              <td>mu-card 卡片式包含一組特定數據的紙片，數據集含有各種相關信息，例如，關於單一主題的照片，文本，和連結。</td>
            </tr>
            <tr>
              <td>Carousel</td>
              <td>mu-carousel 組件用於網站首頁廣告圖切換
                輪播</td>
            </tr>
            <tr>
              <td>
                <mu-chip color="#e1bee7">
                  Chip
                </mu-chip>
              </td>
              <td>mu-chip 是一種小塊的用於呈現複雜實體的塊。比如說歷史事件或聯絡人，薄片<mark>適用大量清單中某些需要關注的資料</mark></td>
            </tr>
            <tr>
              <td>Data Table</td>
              <td>mu-data-table用於顯示表格數據，並且通常用於桌面企業產品中<br>數據表格，<mark>推薦使用</mark>
              </td>
            </tr>
            <tr>
              <td>Dialog</td>
              <td>mu-dialog 用於提示用戶做一些決定，或者是完成某個任務時需要的一些其他額外的訊息
                <br>對話框，<mark>推薦使用，彈跳出來的對話框</mark></td>
            </tr>
            <tr>
              <td>Divider</td>
              <td>mu-divider 主要用於管理和分隔列表和頁面布局的內容
                <mu-divider></mu-divider>分隔線，<mark>類似 hr 標籤</mark>
              </td>
            </tr>
            <tr>
              <td>Drawer</td>
              <td>mu-drawer 從側邊彈出的對話框，<mark>側邊的導覽列</mark></td>
            </tr>
            <tr>
              <td>Expansion Panel</td>
              <td>mu-expansion-panel 包含創建流和允許輕量級編輯的一個元素
                <mu-expansion-panel style="background-color:rgb(211, 211, 211)">
                  <div slot="header">擴展面板</div>
                  <mark>推薦使用，可以將大量資料隱藏在標題下面顯示與隱藏</mark>
                </mu-expansion-panel>
              </td>
            </tr>
            <tr>
              <td>Grid List</td>
              <td>mu-grid-list 網格列表示一種標準列表圖示的可選組件。網格列表與應用於布局和其他可視圖中的網格有著明顯區別。<br>網格列表，<main>適用大量圖片的網格顯示</main>
              </td>
            </tr>
            <tr>
              <td>Form</td>
              <td>mu-form 用於實現表單布局以及提供簡單的驗證功能。<br>
                <mark>推薦使用，輸入表單，有一般輸入、日期輸入及輸入後的表單驗證</mark>
              </td>
            </tr>
            <tr>
              <td>Date Input</td>
              <td>mu-data-input 用於選擇日期。將mu-text-field、mu-data-picker、mu-time-picker 組合在一起行稱一個新的組件。,<br>時間輸入框，<mark>設定開始時間與結束時間</mark>
              </td>
            </tr>
            <tr>
              <td>Select</td>
              <td>mu-select 用於從選擇列表中收集用戶提供的訊息<br>
                選擇框，<main>推薦使用</main>
              </td>
            </tr>
            <tr>
              <td>Select Controls</td>
              <td>選擇控件允許用戶選擇的選項，分為 mu-checkbox，mu-radio和 mu-switch<br><mark>推薦使用</mark></td>
            </tr>
            <tr>
              <td>Text Field</td>
              <td>mu-text-field 用於收集用戶提供的訊息
                <br>文本輸入框，<mark>必定使用</mark><br>
                <mu-text-field placeholder="Please input......"></mu-text-field><br />
              </td>
            </tr>
            <tr>
              <td>Auto Complete</td>
              <td>mu-auto-complete 是普通文本輸入框的擴展，可以動態的自選完成輸入<br>自動補全輸入，<mark>配合上面的 mu-text-field</mark>
              </td>
            </tr>
            <tr>
              <td>List</td>
              <td>mu-list 列表作為一個單一的連續元素以垂直排列的方式顯示多行條目<br>列表，<mark>推薦使用</mark>
              </td>
            </tr>
            <tr>
              <td>LoadMore</td>
              <td>mu-load-more 內置下拉加載和無線滾動功能<br>
                加載控件，<mark>預先顯示5個內容，下拉後再顯示內容</mark>
              </td>
            </tr>
            <tr>
              <td>Menu</td>
              <td>mu-menu 組件展示一個菜單用於激活它的元素的位置上<br>菜單，<mark>配合 mu-appbar、mu-bottom-navigation..等導覽列使用</mark>
              </td>
            </tr>
            <tr>
              <td>Pagination</td>
              <td>mu-pagination 組件用於分隔數據集<br>分頁，<mark>好幾頁的分頁按鈕</mark>
              </td>
            </tr>
            <tr>
              <td>Paper</td>
              <td>mu-paper 是一個陰影效果的容器<br>紙片，<mark>包裹一個內容、圖片..等的陰影設定</mark>
              </td>
            </tr>
            <tr>
              <td>Date Picker</td>
              <td>mu-date-picker 用於選擇日期<br>日期選擇器，
                <mark>配合上面的其他組件使用</mark>
              </td>
            </tr>
            <tr>
              <td>Time Picker</td>
              <td>
                mu-time-picker 用於時間選擇 <br>時間選擇器，
                <mark>配合上面的其他組件使用</mark>
              </td>
            </tr>
            <tr>
              <td>Popover</td>
              <td>mu-popover 用於彈出提示，它封裝在 mu-meun 組件內<br>彈出框
              </td>
            </tr>
            <tr>
              <td>Progress</td>
              <td>mu-linear-progress 和 mu-circular-progress 用於向用戶顯示數據加載進度 <br>進度條，
                <mark>直條進度、圓形進度</mark>
              </td>
            </tr>
            <tr>
              <td>Slide Picker</td>
              <td>mu-slide-picker 用於行動裝置上的滑動選擇器<br>滑動選擇器
              </td>
            </tr>
            <tr>
              <td>Slider</td>
              <td>mu-slider 用於一個固定區間內進行選擇<br>
                滑塊
              </td>
            </tr>
            <tr>
              <td>Snackbar</td>
              <td>mu-snackbar 是一種針對操作的輕量級反饋機制，常以一個小的彈出框形式 <br>小吃吧，
                <mark>有趣使用，但如有需要 push 資料，目前尚無法解決</mark>
              </td>
            </tr>
          </table>
        </mu-expansion-panel>
        <li>
          <h3>Muse-ui 提供的工具?</h3>
          <ul>
            <li><span>使用工具前需先引入檔案</span>
              <pre class="prettyprint">
//main.js
import Vue from 'vue';
import Helpers from 'muse-ui/lib/Helpers';
Vue.use(Helpers);</pre>
            </li>
            <li>
              <h4>Transitions 過度動畫?</h4>
              <a
                href="https://muse-ui.org/#/zh-CN/transitions"
                target="_blank"
              >Transitions 過度動畫</a>
              <ul>
                <li>Fade 淡出淡入，原地淡入淡出</li>
                <li>Silde 滑入滑出，上下左右四個方位滑入滑出</li>
                <li>Expand 展開摺疊，上下摺疊</li>
                <li>Scale 縮放，原地縮放</li>
              </ul>
            </li>
            <li>
              <h4>Directives 指令?</h4>
              <a
                href="https://muse-ui.org/#/zh-CN/directives"
                target="_blank"
              >Directives 指令</a>
              <ul>
                <li>ClickOutside，點進與點出，觸發兩種狀態</li>
                <li>Resize，響應式，接收當前 width 與 height</li>
                <li>Resize，接收當前 ScrollTop 的位置</li>
                <li>Elevation，數值型拉霸</li>
              </ul>
            </li>
            <li>
              <h4>Ripple 波紋組件?</h4>
              <a
                href="https://muse-ui.org/#/zh-CN/ripple"
                target="_blank"
              >Ripple 波紋</a>
            </li>
            <li>
              <h4>為何不能使用 muse-ui Ripple ?</h4>
              <p>需事前把三個 loader 安裝起來，less、less-lasder、babel-plugin-import，即可正常使用
              </p>
            </li>
          </ul>
        </li>
        <li>
          <h3>Muse-ui 常用的 Grid 網格?</h3>
          <a
            href="https://muse-ui.org/#/zh-CN/grid"
            target="_blank"
          >Grid 網格</a>
          <ul>
            <li>
              <pre class="prettyprint">
&lt;mu-container class="demo-container is-stripe">
&lt;mu-row gutter>
  &lt;mu-col span="8" sm="6" md="4" lg="3" xl="1">
      &lt;div class="grid-cell">&lt;/div>
  &lt;/mu-col>
  &lt;mu-col span="4" sm="6" md="8" lg="9" xl="11">
      &lt;div class="grid-cell">&lt;/div>&lt;/mu-col>
  &lt;mu-col span="4" sm="6" md="8" lg="9" xl="11">
      &lt;div class="grid-cell">&lt;/div>&lt;/mu-col>
  &lt;mu-col span="8" sm="6" md="4" lg="3" xl="1">
      &lt;div class="grid-cell">&lt;/div>&lt;/mu-col>
&lt;/mu-row>
&lt;/mu-container></pre>
              <ul>
                <li>預設四個響應式尺寸:sm、md、lg和xl</li>
                <li>sm >= 576px</li>
                <li>md >= 768px</li>
                <li>lg >= 992px</li>
                <li>xl >=1200px</li>
                <li>使用 span 或是四個響應式尺寸去調整寬度，最大 12 最小 1，合計水平12</li>
                <li>CSS 設定上可以直接使用 .col、.row 去修改樣式</li>
              </ul>
              <pre class="prettyprint">
&lt;mu-row gutter>
  &lt;mu-col span="6">
    &lt;div class="grid-cell">&lt;/div>
  &lt;/mu-col>
  &lt;mu-col span="6">&lt;
    div class="grid-cell">&lt;/div>
  &lt;/mu-col>
&lt;/mu-row></pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>Muse-ui 常用的 layout 布局?</h3>
          <ul>
            <li>
              <pre class="prettyprint">
&lt;mu-flex justify-content="center">&lt;/mu-flex>

justify-content  start/center/end/between/around
wrap             wrap/nowrap/wrap-reverse
align-items      start/center/end/baseline/stretch
direction        row/column/row-reverse/column-reverse</pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>Muse-ui 使用色彩與Icon?</h3>
          <a
            href="https://muse-ui.org/#/zh-CN/color"
            target="_blank"
          >Color 色彩</a>&nbsp;<a
            href="https://material.io/tools/icons/?style=baseline"
            target="_blank"
          >Icon 圖標</a>
          <ul>
            <li>
              <pre class="prettyprint">
//組件中使用 color
&lt;mu-button color="#1565c0"> Custom &lt;/mu-button>          //組件中使用 icon
&lt;mu-icon value="home">&lt;/mu-icon>
              </pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>Muse-ui 環境設定?</h3>
          <ul>
            <li>
              <pre class="prettyprint">
//npm 安裝
npm i muse-ui -S
//目錄
index.html
main.js
//index.html，使用 Roboto Font 與 Font Icons 樣式
&lt;link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,400italic">
&lt;link rel="stylesheet" href="https://cdn.bootcss.com/material-design-icons/3.0.1/iconfont/material-icons.css">
//main.js，引用 Muse-ui
import Vue from 'vue';
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
Vue.use(MuseUI);
new Vue({
  el: '#app',
  render (h) {
    return h('mu-button', {}, 'Hello World');
  }
});</pre>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "muse-ui"
  };
</script>

<style lang="scss" scoped>
  @import "../../../style/common.scss";
  @import "../../../style/style.scss";
  table {
    border-spacing: 0;
  }
  td {
    padding: 0.2rem;
  }
  td:first-child,
  th:first-child {
    border-radius: 5px 0 0 5px;
  }
  td:last-child,
  th:last-child {
    border-radius: 0 5px 5px 0;
  }
  tr:nth-of-type(2n + 1) {
    background-color: $color3;
  }
</style>