<template>
  <div>
    <h2>Webpack QTL</h2>
    <!--      
      section>(div>h2)(div>span+span+pre.prettyprint)
    -->
    <section>
      <div>
        <h3>目錄說明</h3>
      </div>
      <div class="OneFields">
        <pre class="prettyprint">
vuep //專案名稱
T : terminal  //開啟終端
R : router.js  //網頁路由管理
S : store.js  //vuex 管理
App : App.vue //Vue 的入口設定檔案
M : main.js  //Vue 的入口 JS 檔案
V : views  //Vue 放置畫面的組件檔案
C : components  //Vue 放置插入畫面內組件檔案
A : assets //放置靜態檔案，如 CSS、Image..等</pre>
      </div>
    </section>
    <section class="OneFields">
      <div>
        <h3>指令</h3>
      </div>
      <div>
        <span>T</span><span>Ctrl + ` 開啟中終端</span>
        <pre class="prettyprint">
$npm run dev //啟動 http server 後，開啟 index.js 與 main.js
             //是本地開發的時候除錯用
$npm run build //開始將所有 JS 檔案打包到 dist 目錄成一個 bundle.js
          </pre>
      </div>
    </section>
    <section class="TwoFields">
      <div>
        <h3>新增頁面</h3>
        <div>
          <p>新增一個 Vue 路由網頁。</p>
          <pre class="prettyprint">
vuep
|-src
  |-views              
router.js</pre>
        </div>
      </div>
      <div>
        <span>V</span><span>Hello2.vue</span>
        <pre class="prettyprint">
&lt;template&gt;
  HTML...
&lt;/template&gt;
&lt;script&gt;
  export default{
    name:'Hello2'
  }
&lt;/script&gt;
          </pre>
      </div>
      <div>
        <span>R</span><span>router.js</span>
        <pre class="prettyprint">
export default new Router({
  routes:[
    {
      path: '/hello2', //路由路徑
      name: 'hello2',  //名稱
      component: () => import('./views/Hello2.vue') //使用組件路徑
    }
  ]
})</pre>
      </div>
    </section>
    <section class="TwoFields">
      <div>
        <h2>使用 router 方式</h2>
        <div>
          <p>新增 Hello2.vue 的 View</p>
        </div>
      </div>
      <div>
        <span>R</span><span>router.js 方式一</span>
        <pre class="prettyprint">
import Hello2 from '@/views/Hello2' //使用組件路徑

export default new Router({
  routes:[  //設定路徑
    {
      path: '/hello2', //自訂路徑
      name: 'hello2',  //設定名稱
      component: Hello2 //選擇要對應的元件
    }
  ]
}) </pre>
      </div>
      <div>
        <span>R</span><span>router.js 方式二</span>
        <pre class="prettyprint">
export default new Router({
  routes:[ //設定路徑
    {
      path: '/hello2', //自訂路徑
      name: 'hello2',  //設定名稱
      component: () => import('./views/Hello2.vue') //使用組件路徑
    }
  ]
})</pre>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name: "webpack_QTL"
  };
</script>

<style lang="scss">
  @import "../../../style/common.scss";
  @import "../../../style/pretty.scss";
  @import "../../../style/style.scss";
</style>
