      <!-- li>h3+(ul>li>pre.prettyprint) -->
      <ul>
        <li class="a">
          <h3>Git 基本指令有哪些?</h3>
          <table>
            <tr>
              <th>指令</th>
              <th>說明</th>
            </tr>
            <tr>
              <td>$ git init</td>
              <td>使用 git 對當前目錄進行版本控制</td>
            </tr>
            <tr>
              <td>$ git add --all</td>
              <td>一口氣把全部的檔案加入到暫存區</td>
            </tr>
            <tr>
              <td>$ git status</td>
              <td>是用來查詢現在目錄的狀態 (status 記錄此目錄歷史的指令與狀態)</td>
            </tr>
            <tr>
              <td>$ git commit -m "初始化 init"</td>
              <td>將暫存區的內容永久的存活下來，"初始化 init"為此次commit 的名稱</td>
            </tr>
            <tr>
              <td>$ git commit --allow-empty -m "增加一個空的 commit"</td>
              <td>做一個空的 commit</td>
            </tr>
            <tr>
              <td>$ git commit -a -m "update content"</td>
              <td>直接省略 add 動作直接到 commit，但對新加入的檔案無效</td>
            </tr>
            <tr>
              <td>$ git log </td>
              <td>檢視所有 commit 的詳細資料</td>
            </tr>
            <tr>
              <td>$ git log --oneline</td>
              <td>檢視精簡的 commit 的資料</td>
            </tr>
            <tr>
              <td>$ git commit --amend</td>
              <td>進入 Vim 模式修改</td>
            </tr>
            <tr>
              <td>$ git commit --amend -m "更改名稱"</td>
              <td>如此可以修改最後一次 commit 的訊息</td>
            </tr>
            <tr>
              <td>$ git reflog</td>
              <td>找尋被刪除的 commit (可以在30天內找回誤刪除的 commit )</td>
            </tr>
            <tr>
              <td>$ git log -p dog.txt</td>
              <td>檢視 dog.txt 的commit 紀錄</td>
            </tr>
            <tr>
              <td>$ git blame index.html </td>
              <td>這個檔案的程式碼每行的修改紀錄
              </td>
            </tr>
            <tr>
              <td>$ git reset e12d8ef^</td>
              <td>拆解e12d8ef這個的 commit</td>
            </tr>
            <tr>
              <td>$ git branch</td>
              <td>檢視當前的分支</td>
            </tr>
            <tr>
              <td>$ git branch cat</td>
              <td>新增一個名稱為 cat 的分支</td>
            </tr>
            <tr>
              <td>$ git branch -m cat tiger</td>
              <td>修改分支的名稱(git branch -m舊名稱 新名稱)</td>
            </tr>
            <tr>
              <td>$ git branch -d dog</td>
              <td>刪除 dog這個分支</td>
            </tr>
            <tr>
              <td>$ git branch -D dog</td>
              <td>強制刪除尚未合併的 dog 分支</td>
            </tr>
            <tr>
              <td>$ git checkout cat</td>
              <td>切換到 cat分支</td>
            </tr>
            <tr>
              <td>$ git checkout b1ea289</td>
              <td>將當前的 HEAD 切換至b1ea2895 這個 commit 上</td>
            </tr>
            <tr>
              <td>$ git merge cat</td>
              <td>合併 cat 這個分支</td>
            </tr>
          </table>
        </li>
        <li>
          <h3>Git 操作技巧有哪些?</h3>
          <ul>
            <li>
              <h4>如何 Push 上傳 GitHub?</h4>
              <a
                href="https://gitbook.tw/chapters/github/push-to-github.html"
                target="_blank"
              >如何 Push 上傳到 GitHub？</a>
              <pre class="prettyprint">
//0.準備前面的 git init、git add、git commit…等等
//1.準備把東西推上遠端的Git 伺服器。首先需要設定一個端節的節點
$ git remote add origin git@github.com:kaochenlong/practice-git.git
$ git push -u origin master //2.推上東西
//2-1.如果你能理解上面這個指令的意思，你就可以再做一些變化。例如你的遠端節點叫做 dragonball，而且你想把 cat 分支推上去：
$ git push dragonball cat</pre>
            </li>
            <li>
              <h4>想要刪除某幾個 Commit 或是調整 Commit 的順序?</h4>
              <a
                href="https://gitbook.tw/chapters/rewrite-history/remove-and-reorder-commit.html"
                target="_blank"
              >想要刪除某幾個 Commit 或是調整 Commit 的順序</a>
              <p>移動 Commit...</p>
            </li>
            <li>
              <h4>
                把多個 Commit 合併成一個 Commit?
              </h4>
              <a
                href="https://gitbook.tw/chapters/rewrite-history/merge-multiple-commits-to-one-commit.html"
                target="_blank"
              >把多個 Commit 合併成一個 Commit</a>
              <pre class="prettyprint">
$ git log --oneline
27f6ed6 (HEAD -> master) add dog 2
2bab3e7 add dog 1
ca40fc9 add 2 cats
1de2076 add cat 2
cd82f29 add cat 1
382a2a5 add database settings
bb0c9c2 init commit                

$git rebase -i bb0c9c2 
//在互動模式的紀錄由上而下是從最舊到最新，跟 git log 指令所呈現的結果是相反的。
//要開始處理的 commit，接著一樣再次出現 Vim 編輯器視窗
pick 382a2a5 add database settings
pick cd82f29 add cat 1
pick 1de2076 add cat 2
pick ca40fc9 add 2 cats
pick 2bab3e7 add dog 1
pick 27f6ed6 add dog 2

# Rebase bb0c9c2..27f6ed6 onto bb0c9c2 (6 commands)
#
# Commands:
# ...[略]...
//使用 squash 指令，將上面內容修改如下
pick 382a2a5 add database settings
pick cd82f29 add cat 1
squash 1de2076 add cat 2
squash ca40fc9 add 2 cats
pick 2bab3e7 add dog 1
squash 27f6ed6 add dog 2
//存檔並離開，會開始進行 Rabase</pre>
              <p>上面所發生的事情是<br>
                1.最後一行的 27f6ed6 會跟前一個 Commit 2bab3e7 進行合併，也就是 add dog 1 跟 add dog 2 這個 Commit 會合在一起。<br>
                倒數第三號的 ca40fc9 會跟前一個 Commit 1de2076 合併，但因為 1de2076 又會再往前一個 Commit cd82f29 合併，所以整個跟 cat 有關的這三個 Commit 會併成同一個。</p>
            </li>
            <li>
              <h4>我可以從過去的某個 Commit 再長一個新的分支出來嗎？</h4>
              <a
                href="https://gitbook.tw/chapters/branch/branch-from-old-commit.html"
                target="_blank"
              >我可以從過去的某個 Commit 再長一個新的分支出來嗎？</a>
              <pre class="prettyprint">$ git checkout 657fce7 //移動到那個分支
$ git checkout -b brid //你可以從現在這個 Commit 開新的分支出去，讓我們直接使用 Checkout 指令的 -b 參數直接建立分支並自動切換過去</pre>
            </li>
            <li>
              <h4>合併發生衝突了，怎麼辦？</h4>
              <a
                href="https://gitbook.tw/chapters/branch/fix-conflict.html"
                target="_blank"
              >合併發生衝突了，怎麼辦？</a>
              <p>去看合併有問題的檔案，並直接看要使用哪一分支的內容，最後add 與 commit 完成合併，完成合併</p>
            </li>
            <li>
              <h4>查詢被刪掉的分支?</h4>
              <p>你可以從 git reflog 指令去翻翻看，Reflog 預設會保留 30 天，所以 30 天內應該都還找得到。</p>
              <pre class="prettyprint">$ git reflog</pre>
            </li>
            <li>
              <h4>合併兩個分支?</h4>
              <p>我從 master 分支開了一個 cat 分支，並且做了兩次 Commit。
                任務執行的差不多了，就要準備合併回來了。如果我想要 master 分支來合併 cat 分支的話，我會先切回 master 分支
              </p>
              <pre class="prettyprint">$ git checkout master //切換分支
$ git merge cat //合併 cat 分支</pre>
              <p>在 cat 分支新增的 cat1.html 跟 cat2.html，因為 master 現在已經合併 cat 分支，所以現在在 master 分支也有一份了。</p>
            </li>
            <li>
              <h4>切換分支的檔案?</h4>
              <p>目前 master 分支有4個檔案，切換到 cat 分支後新增兩個檔案，再切換到 master 分支。<br>
                咦？剛剛那兩個檔案不見了！別擔心，其實他們都還在的，只是在不同的分支而已，只要切回(checkout) cat 分支，檔案就會出現了。
              </p>
            </li>
            <li>
              <h4>HEAD 是什麼東西？</h4>
              <p>HEAD 是一個指標，指向某一個分支，通常你可以把 HEAD 當做「目前所在分支」看待。</p>
            </li>
            <li>
              <h4>在 Vim 模式中操作修改最後一個 commit 的名稱 ?</h4>
              <pre class="prettyprint">
$ git commit –amend // 開始修改最後一個 commit
e  //輸入e，進入編輯
a //輸入a，進入 append，開始修改名稱
esc //按下後退到 Normal 模式
:wq //進行儲存並關閉這個檔案</pre>
            </li>
            <li>
              <h4>為什麼要二段式這麼麻煩？</h4>
              <p>先 add 再 commit 這樣二段式感覺可能有點麻煩，但也是有好處的。<mark>你可以想像你有一個倉庫，在倉庫門口有個小廣場，這個廣場的概念就像跟暫存區一樣，你把要存放到倉庫的貨物先放到這邊（git add），然後等收集的差不多了就可以打開倉庫門，把在廣場上的貨物送進倉庫裡（git commit），並且記錄下來這批貨是什麼用途的、誰送來的。</mark><br>當然，你也可以每來一件貨物就開倉門存一次、紀錄一次，但這樣一來開倉庫的次數就會非常多。老實說這樣也沒什麼過錯，只是因為太過零碎的 Commit，有時候要查閱紀錄可能會有點不太方便；另外，過於零碎的 Commit 也可能給你的同事帶來一些困擾，例如要進行 Code Review 的時候，比較有整理的 Commit 可以一次看到比較完整的內容，而不需要一個一個 Commit 慢慢看。
              </p>
            </li>
            <li>
              <h4>所以，什麼時候要 Commit？</h4>
              <p>
                這個問題沒有標準答案，你可以很多檔案修改好再一口氣全部一起 Commit，也可只改一個字就 Commit。常見的 Commit 的時間點有：<br>
                1.修改幾個字<br>
                2.完成一個頁面<br>
                3.完成一個進度<br>
                4.完成一個小系統<br>
                5.下班前來一個 Commit<br>
                x.你想要 Commit 的時候就可以 Commit。</p>
            </li>
            <li>
              <h4>平行時空，關於修改 Commit 名稱的時機?</h4>
              <p>
                記得在 Git 中 Push 出去之後，就不要再更改 Commit 的訊息，否則可能會造成其它人的困擾。</p>
            </li>
          </ul>
        </li>
        <li>
          <h3>如何將本地端資料上傳到 GitHub?</h3>
          <a
            href="https://git-scm.com/download/win"
            target="_blank"
          >0.安裝網址</a><br>
          <a
            href="https://gitbook.tw/chapters/environment/install-git-in-windows.html"
            target="_blank"
          >1.GitHub 設定指引</a><br>
          <a
            href="https://adon988.logdown.com/posts/7815041-to-resolve-ssh-add-appears-could-not-open-a-connection-to-your-authentication-agent"
            target="_blank"
          >2. 解決，ssh add 出現 Could not open a connection to your authentication agent.</a><br>
          <a
            href="https://ithelp.ithome.com.tw/articles/10205988?sc=iThelpR"
            target="_blank"
          >3. 設定 Github SSH 金鑰 feat. Github SSH、HTTPS 的差異</a><br>
          <a
            href="https://gitbook.tw/chapters/github/fail-to-push.html"
            target="_blank"
          >4. 怎麼有時候推不上去…</a>
          <ul>
            <li>
              <h4>安裝 Git</h4>
              <p>-至 Git 官網安裝軟體，選擇好版本後，安裝就可以一路按下一步按到底<br>
                -安裝完成，<mark>電腦上搜尋 Git Bash 並開啟它</mark>，它類似命令提示字元的小黑框(它本身模擬了一個在 Linux 的世界還滿有知名度的軟體)<br>
                -體驗一下Git 是不是有安裝起來，以及版本資訊對不對
                <pre class="prettyprint">
$ which git //git 安裝位址
$ git --version //git 目前版本</pre>
              </p>
            </li>
            <li>
              <h4>開啟 Git Base</h4>
              <p>本基端產生 SSH Key 的方法?</p>
              <pre class="prettyprint">
//輸入指令，獲得 SSH Key
$ ssh-keygen -t rsa -C "your_email@example.com" 
// "" 內輸入自己的信箱
//接著按下 3次 Enter，略過 passphrase 等等
//後會顯示我們的 SSH Key 的 fingerprint

//SSH key
The key fingerprint is:
SHA256:sqKOi11S8Lg26TOOOA+gFIn7XXGyr9OI7BqDOO w53652456@gmail.com</pre>
              <p>透過 SSH Key 自動登入 GitHub?</p>
              <pre class="prettyprint">
//輸入指令，啟動 ssh-agent
//在 SSH Key 的情況下，才可以執行 git 的 clone 等操作
$ ssh-agent -s
//將剛剛產生的 key 加入 ssh-agent
$ ssh-add ~/.ssh/id_rsa
//若無法出現錯誤的話，再次輸入此行指令，就可以執行 shh-add
ssh-agent bash</pre>
              <p>獲得讓 GitHub 使用的 SSH Key?</p>
              <pre class="prettyprint">
//輸入指令，獲得 SSH key           
clip &lt;~/.ssh/id_rsa.pub     

ssh-rsa OOOOB3NzaC1yc2EAAAADAQABAAABAQDH8GYUWNDSVPPxLo+ibE0gPngPhBWI5wm1OpUWY2LH
d0UKGeKcVi+BBB1C5RcwxS2YnlY+05ZEfuBpbGOQpfUO2aSgLt/FiDNI3dGBhFvPUybD+OOOOOOVcYjB
TqOOOOOOOJyn/br9sV39fw7W14YLT7WX4kv1OOOOOOOOUepu1SSb3fna1Y7Di9SqQE5PYHJM4ia+or/7
H7/wSWLq89Ldu3kNvfxyvcj/fz5OB/R8kCnTI0eYM1Ozy3oiJB8f0uPltl8w4bOOOOOOOOOabxMiAmTV
Dgcm2DZRxpKPL44rXJr3HQk0MA0ub w53652456@gmail.com
              </pre>
            </li>
            <li>
              <h4>GitHub 上添加 SSH Key</h4>
              <p>如何添加 SSH Key?<br>
                -在自己的 GitHub 頭像上點選 Settings 進入找到 SSH and GPG keys<br>
                -將上面獲得的 SSH key 右鍵貼上 'key' 上，並為這個 'key' 命一個 'title' 的名稱<br>
                -完成後點擊 ADD SSH key 完成使用
              </p>
              <p>如何驗證是否有使用成功?<br>
                -開啟 Git Bash 並輸入指令
                <pre class="prettyprint">
$ ssh -T git@github.com
//有看到專案的名稱，即成功使用 SSH Key 於 GitHub 上</pre>
              </p>
            </li>
            <li>
              <h4>同步</h4>
              <p>如何從本機端上傳到 GitHub 上?</p>
              <pre class="prettyprint">
$ git remote add origin https://github.com/azpact/azpact.github.io.git
//第一次需要這樣撰寫 (指令當前要上傳的目標位置)
$ git push -u origin master
//第一次需要這樣撰寫 (指令當前要上傳的是哪個分支名稱)
$ git push
//完成前兩個指令後，就可以直接以此指令將本機端的資料上傳到 GitHub 上(除非有要上傳到其他專案或分支)</pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>怎麼有時 Git Push 不上去?</h3>
          <p>通常這個狀況會發生在多人一起開發的時候，想像一下這個情境：<br>
            1.Sherly 跟 Eddie 兩個人在差不多的時間都從 Git Server 上拉了一份資料下來準備進行開發。<br>
            2.Sherly 手腳比較快，先完成了，於是先把做好的成果推一份上去。<br>
            3.Eddie 不久後也完成了，但當他要推上去的時候發現推不上去了…<br>
          </p>
          <ul>
            <li>
              <h4>解決方法一，先拉再推</h4>
              <pre class="prettyprint">
$ git pull –rebase //你應該先拉一份線上版本的回來更新，然後再推一次
// --rebase 參數是表示「內容抓下來之後請使用 Rebase 方式合併
$ git push</pre>
            </li>
            <li>
              <h4>解決方法二，直接推</h4>
              <pre class="prettyprint">
$ git push -f //直接無視之前的 commit，強迫推上去</pre>
            </li>
            <li>
              <h4>解決方法三，Git錯誤non-fast-forward後的衝突解決</h4>
              <a
                href="https://blog.csdn.net/sinat_25306771/article/details/55257901"
                target="_blank"
              >Git錯誤non-fast-forward後的衝突解決</a>
              <pre class="prettyprint">
//輸入兩個指令        
$ git config branch.master.remote origin
$ git config branch.master.merge refs/heads/master
$ git pull //再重新 git pull，就可以pull你的代碼
$ git push //最後 git push</pre>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

