      <!-- li>h3+(ul>li>pre.prettyprint) -->
      <ul>
        <li>
          <h3>Vue 與 Vuex 之間怎麼互動?</h3>
          <p><mark>組件中分成兩個目錄， views 與 components，views 為放置畫面的組件，components 放置重複性使用的組件。</mark><br>以作者個人習慣來說，會將所有 data 資料放置於 Vuex 中，透過 action、mutation..等狀態改變提供 data 到 views 內的組件，再透過 prop 將 data 資料給 components 內的重複性組件。</p>
          <table style="text-align:center">
            <tr>
              <td
                colspan="2"
                class="c"
              >調度到 Actions</td>
              <td
                colspan="3"
                class="a"
              >Vuex</td>
            </tr>
            <tr>
              <td
                class="c"
                rowspan="6"
              >Dispatch<br>(調度)</td>
              <td
                class="c"
                rowspan="6"
              >↱</td>
              <td class="a">Actions</td>
              <td class="a">⬌</td>
              <td class="a">Getters</td>
            </tr>
            <tr class="a">
              <td>⬇</td>
              <td>Commit</td>
              <td rowspan="5">&nbsp;</td>
            </tr>
            <tr class="a">
              <td>Moutations</td>
              <td></td>
              <td></td>
            </tr>
            <tr class="a">
              <td>⬇</td>
              <td>Mutate</td>
            </tr>
            <tr class="a">
              <td>State</td>
              <td></td>
              <td></td>
            </tr>
            <tr class="a">
              <td>⬇</td>
              <td>Render<br>(給予)</td>
              <td></td>
            </tr>
            <tr class="b">
              <td rowspan="3">vue</td>
              <td>views</td>
              <td>State(data)</td>
              <td></td>
              <td rowspan="3"></td>
            </tr>
            <tr class="b">
              <td>↑<br>$emit</td>
              <td>⬇</td>
              <td>props</td>
            </tr>
            <tr class="b">
              <td>components</td>
              <td>State(data)</td>
              <td></td>
            </tr>
          </table>
        </li>
        <li>
          <h3>Vuex 小細節程式撰寫?</h3>
          <ul>
            <li>
              <h4>Vuex基本環境設定?</h4>
              <pre class="prettyprint">
//目錄
src
|-store
  |-modules
   |-todo.js
  |-index.js
main.js
//main.js
import store from "./store";
new Vue({
  store,
  render: h => h(App)
}).$mount("#app");
//index.js
import Vue from "vue";
import Vuex from "vuex";
import todos from "./modules/todos";
Vue.use(Vuex);
export default new Vuex.Store({
  modules: {
    todos
  }
});
//todo.js
const state = {}
const getter = {}
const actions = {}
const mutations = {}
export default {
 state, //放置資料
 getters, //放置 getter 方法
 actions, //可以是異步操作
 mutations //只能同步操作
}</pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>如何設定 Vuex 與 axios 環境?</h3>
          <a
            href="https://codesandbox.io/s/axiostest-g2fhb"
            target="_blank"
          >axiosTest</a>
          <ul>
            <li>
              <pre class="prettyprint">
//安裝 vuex 與 axios
$ npm install -g vuex axios
//目錄
src
|-views
  |-todos.vue
store
|-modules
  |-todo.js
|-index.js
App.js
main.js
//main.js，引用 store 目錄
import store from "./store";
new Vue({
  store,
  render: h => h(App)
}).$mount("#app");
//index.js，設定 vuex 相關設定
import Vue from "vue";
import Vuex from "vuex";
import todos from "./modules/todos";
Vue.use(Vuex);
export default new Vuex.Store({
  modules: {
    todos
  }
});
//todo.js，專門的 vuex js 設定
import axios from "axios"; //引用 axios
const state = { //初始 state 狀態 
  todos: ["000"]
};
const getters = { //設定 getter
  allTodos: state => state.todos
};
const actions = {  //設定 actions
  async fetchTodos({ commit }) {
    const url = "https://api.jsonbin.io/b/5d0ae2d4748415302670a9e1";
    const response = await axios({
      methods: "get",
      url: url,
      headers: {
        "secret-key":
          "$2a$10$XflNqIVbG42ADERhOMsvp.KChlqUCodB/fEQFGSdjmIfKOqonOJci"
      },
      timeout: 6000
    })
      .then(function(response) {
        commit("setTodos", response.data);
        console.log(response.data);
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  async addTodo({ commit }, title) {
    let url = "https://jsonplaceholder.typicode.com/todos";
    const response = await axios.post(url, { title, completed: false });
    commit("newTodo", response.data);
  },
  async deleteTodo({ commit }, id) {
    await axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`);
    commit("removeTodo", id);
  },
  async filterTodos({ commit }, e) {
    //Get select number
    const limit = parseInt(
      e.target.options[e.target.options.selectedIndex].innerText
    );
    const response = await axios.get(
      `https://jsonplaceholder.typicode.com/todos?_limit=${limit}`
    );
    commit("setTodos", response.data);
  }
};
const mutations = {
  setTodos: (state, todos) => (state.todos = todos), //此括號內的 todos 為使用左邊進來的參數
  newTodo: (state, todo) => state.todos.unshift(todo),
  removeTodo: (state, id) =>
    (state.todos = state.todos.filter(todo => todo.id !== id))
};
export default {
  state,
  getters,
  actions,
  mutations
};
//todos.vue，要與 vuex 互動的組件
&lt;template>
  &lt;div>
    &lt;h3>Todos&lt;/h3>
    &lt;div class="todos">
      &lt;div v-for="(val,key) in allTodos" :key="key" class="todo">{\{key}} // {\{val}}&lt;/div>
    &lt;/div>
  &lt;/div>
&lt;/template>
&lt;script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Todos",
  data() {
    return {
      row: ["123"]
    };
  },
  methods: {
    ...mapActions([
      "fetchTodos" //, "deleteTodo"
    ])
  },
  computed: mapGetters(["allTodos"]),
  mounted() {
    this.fetchTodos();
  }
};
&lt;/script>
//App.vue
&lt;template>
  &lt;div>
    &lt;div class="container">
      &lt;Todos>&lt;/Todos>
    &lt;/div>
  &lt;/div>
&lt;/template>
&lt;script>
import Todos from "./components/todos.vue";
export default {
  name: "App",
  components: {
    Todos
  }
};
&lt;/script>
