					<div id="tag-meet-php"><h3>認識PHP</h3></div>
					<div>
						<h4 id="tag-about-website-categories"><ins>01:關於網站類別</ins></h4>					
						<p class="textin2">初步分類網站有兩種，靜態網站及動態網站。靜態網站指以HTML、CSS、JS等語言程式編寫，無資料傳遞、交換..動態互動，反之為動態網站。</p>
						<p class="textin2">要製作動態網站建立方式有許多種，本頁以PHP及其他軟件共同建立與架構動態網站之目的學習。</p>
						<img src="../../assets/image/image19.png">
					</div>
					<div>
						<h4 id="tag-php-backend-architecture"><ins>02:PHP後端架構</ins></h4>					
						<p class="textin2">我們所要建立動態網站為目的，要先了解自己有哪些資源及要使用的應用程式類別。
							<table class="table-style">
								<tr>
									<td>伺服器OS</td>
									<td>如Mac、Window、Unix-Like(伺服器使用的電腦作業系統)</td>
								</tr>
								<tr>
									<td>網站伺服器</td>
									<td>Apache(網站架設伺服器)</td>
								</tr>
								<tr>
									<td>應用程式</td>
									<td>PHP</td>
								</tr>
								<tr>
									<td>資料庫</td>
									<td>MySQL</td>
								</tr>
							</table>
						</p>
						<p class="textin2">
							使用Window作業系統加上後三類，稱wawp，其中現有的wawp有三套appserver、wampserver、XAMPP。
							<b>關於Apache</b><br>
							Apache是解譯器，透過編輯器編寫的.php檔案，經過Apache解譯，將內容轉換成html語言給瀏覽器呈現。
						</p>
					</div>
