					<div>
						<h4 id="tag-write-php-content"><ins>01:寫上PHP語言</ins></h4>			
						<div class="divcode">
              所有PHP的語言都包覆在
              <p>
                &lt;?php<br>
                <br>?>
              </p>
              內容之內
            </div>
					</div>
					<div>
						<h4 id="tag-annotation-method"><ins>02:註解方式</ins></h4>
						<div><b>php單行註解</b>
							<div class="divcode">
                <p>						
								// 第一種方式單行註解<br>
								#  第二種方式單行註解
                </p>
							</div>
						</div>
						<div>
							<b>php多行註解</b>
							<div class="divcode">
								<p>
								/*<br>
								&nbsp;&nbsp;多行註解<br>
								*/
								</p>
							</div>
						</div>
					</div>
					<div>
						<h4 id="tag-about-concatenation">03:關於串接<ins></ins></h4>
						<div>
							【.】串接字串、變數的符號
							<div class="divcode">
								串接字串<br>
								<p>
								echo ('文字一'.'文字二');	<br><br>
								</p>
								串接變數<br>
								<p>
								echo $str.$Happy;	
								</p>
							</div>				
						</div>
					</div>
					<div>
						<h4 id="tag-about-variables"><ins>04:關於變數</ins></h4>					
						<div>
							<b>建立變數</b><br>
							php中 【$】 代表變數，如JavaScript中定義var。
							<div class="divcode">
                <p>var str = "number26";</p>
                //javascript定義變數<br>
								<p>$str = 'number 26';</p>
                //php定義變數<br>
								<p>echo($str);</p>
                //網頁上會顯示，number 26                
							</div>
						</div>
						<div>
							<b>變動變數</b><br>
							可以對定義好的變數，再定義成變數。<br>
							<div class="divcode">
								<p>
								$str = "Happy";<br>
								$$str = "Birthday";<br>
								<br>
								echo $str.'&lt;br>';  //設定變數<br>
								echo $$str.'&lt;br>'; //變動變數<br>
								<br>
								echo $str." ".$Happy; //變動變數<br>
								<br>
								//網頁上顯示<br>
								Happy<br>
								Birthday<br>
								Happy Birthday<br>
							</p></div>
						</div>
						<div>
							<b>連動變數</b><br>
							改變連動對象的內容，但不會改變連動對象的資料類型。<br>
							使用【&amp;】在對象變數前面。<br>
							<div class="divcode">
								<p>
								$var1 = "Happy";  //字串<br>
								$var2 = &amp;$var1;  //字串<br>
								$var2 = 50;  //數值<br>
								<br>
								echo $var1.'&lt;br>'; //$var1變數內容隨著$var2的內容變動<br>
								echo $var2;<br>
								<br>
								//網頁上顯示<br>
								50<br>
								50
								</p>
							</div>
						</div>
						<div>
							<b>預定義變數</b><br>
							快速應用:
							<div class="divcode">
								顯示當前網域位置<br>
								<p>
								echo<br>
                $_SERVER["DOCUMENT_ROOT"]<br>
                .$_SERVER["PHP_SELF"];<br><br>
								//顯示網址如，file:///H:/learn-html/php.html<br>
								</p>
							</div>
						</div>	
						<p>						
							<table class="table-style">
								<tr>
									<td>$_SERVER 服務器和執行訊息<a href="http://php.net/reserved.variables.server" target="_blank">web</a></td>
								</tr>								
								<tr><td style="background-color:#FFF;">顯示此php檔案名稱</td></tr>
								<tr><td style="text-align:left">$_SERVER["PHP_SELF"]</td></tr>
								<tr><td style="background-color:#FFF;">顯示在當前腳本執行的文檔根目錄下</td></tr>
								<tr><td style="text-align:left">$_SERVER["DOCUMENT_ROOT"]</td></tr>								
								<tr><td style="background-color:#FFF;">顯示當前服務器IP位址</td></tr>
								<tr><td style="text-align:left">$_SERVER["SERVER_ADDR"]</td></tr>								
								<tr><td style="background-color:#FFF;">用戶正在查看當前頁面的IP地址</td></tr>
								<tr><td style="text-align:left">$_SERVER["REMOTE_ADDR"]</td></tr>								
								<tr><td style="background-color:#FFF;">讀取虛擬主機名稱</td></tr>
								<tr><td style="text-align:left">$_SERVER["SERVER_NAM"]</td></tr>								
								<tr><td style="background-color:#FFF;">用哪種請求方法訪問頁面; 即'GET'，'HEAD'，'POST'，'PUT'</td></tr>
								<tr><td style="text-align:left">$_SERVER["REQUEST_METHOD"]</td></tr>
							</table>
						</p>
					</div>								
					<div>
						<h4 id="tag-about-conversion-function"><ins>05:關於轉換函數</ins></h4>
						<div>
							關於轉換函數有兩種呈現方式，顯式轉換、隱式轉換。<br>
							<b>顯示函數與隱式函數</b><br>
							等號左右邊呈現出來的方式都一樣。<br>
							顯示函式&nbsp;&nbsp;隱式函數<br>
							intval()=(integer)<br>
							floatval()=(float)<br>
							strval()=(string)<br>
							<b>隱式轉換</b><br>
							將變數類型轉換成另一個變數類型，並且不會改變原始變數的資料類型。<br>
							<div class="divcode">
								<p>
								$var1 = 50;<br>
								$var2 = (float)$var1;<br>
								echo $var1;  //類型為數值<br>
								echo $var2;  //類型為浮點數<br>
								</p>
							</div>
						</div>
						<p><b>隱式函數寫法</b><br>
							隱式函數有四種寫法寫入( )內:<br>
							string【字串】<br>
							integer【整數】<br>
							float【浮數】<br>
							boolean【布林】
						</p>
					</div>
					<div>
						<h4 id="tag-distinguish-the-return-value"><ins>06:判別回傳值</ins></h4>
						<p>
							<b>isset()</b><br>
							判讀變數有無設定值
							<table class="table-style">
								<tr>
									<td>有設定值</td>
									<td>顯示1</td>
								</tr>
								<tr>
									<td>無設定值</td>
									<td>無顯示</td>
								</tr>
							</table>
						</p>
						<div>
							<div class="divcode">
								<p>
									$var1 = ;  //$var1變數中無設定任何值<br>
									echo $isset($var1); //網頁上沒有任何資料顯示									
								</p>
							</div>
						</div>	
						<div>							
							<b>unset()</b><a href="http://php.net/manual/en/function.unset.php" target="_blank">web</a><br>							
							將變數的內容設定成null<br>
							<div class="divcode">
								<p>
									$name = 'Felipe';<br>
									<br>
									echo $unset($name);<br>
									echo $name;<br>
									<br>
									//網頁上顯示<br>
									NULL<br>
									Felipe<br>
								</p>
							</div>
						</div>
						<p>
							<b>empty()</b>
							<table class="table-style">
								<tr>
									<td>變數的內容沒有設定</td>
									<td>回傳true</td>
								</tr>
								<tr>
									<td>變數的內容有設定回傳</td>
									<td>false</td>
								</tr>
							</table>							
						</p>
					</div>
					<div>
						<h4 id="tag-about-the-expression">07:關於運算式<ins></ins></h4>
						<div>
							<b>If...</b>
							使用if有三種方式可以使用<br>
							<div class="divcode">
								第一種方式<br>當if內要做的程式碼只有一行時，可以省略大括號。<br><br>
								<p>
									if($v1 &lt; v2) echo 'v1 is small';<br><br>
								</p>
								第二種方式<br>通用寫if方式。<br><br>
								<p>
									if($v1 &lt; v2){<br>
									&nbsp;&nbsp;echo 'v1 is small;<br>
									}<br><br>
								</p>
								第三種方式<br>想要一次在if內呈現多行程式時，使用【:】與【endif】。<br><br>
								<p>
									if($v1 &lt; v2):<br>
									&nbsp;&nbsp;echo '&lt;br>第一個判別為True';<br>
									&nbsp;&nbsp;echo 'Inside IF';<br>
									else:<br>
									<br>		
									endif								
								</p>
							</div>
						</div>				
						<div>			
							<b>三元運算元</b>
							<div class="divcode">
								<p>
									$v1 = 9;<br>
									$v2 = 3;<br>
									<br>
									echo $v1 &lt; $v2 <ins>?</ins> 'True-符合小於時' <ins>:</ins> 'False-不符合小於時';<br><br>
								</p>
								<ins>?&nbsp;問號</ins>前面表示運算式<br><br>
								運算式成立時，顯示<ins>:&nbsp;冒號</ins>左邊的內容。<br><br>
								運算式不成立時，顯示<ins>:&nbsp;冒號</ins>右邊的內容。<br>
							</div>
						</div>
					</div>
				</section>
				<section>
					<div id="tag-initial-writing-php"><h3>初步編寫PHP</h3></div>
					<div>
