				<section>
					<div id="tag-basic-terms-knowledge"><h3>基本術語/知識</h3></div>
					<div>
						<h4 id="tag-basic-terms"><ins>01:基本術語</ins>
							<a href="#" target="_blank">web</a>
						</h4>					
						<div>
							<b>CGI</b>
							<p class="textin2">CGI 全名為Common Gateway Interface，它的功能是扮演瀏覽器和程式的橋樑，在 Internet 上有許多的通訊協定必須遵守，因此在寫 Internet 程式的過程中，也必須遵照著一些準則，所以當使用者在瀏覽器上下一個需求時，系統會傳回一些訊息給Web Server，而CGI程式的功能就是接收這些訊息後，再用程式加以處理，儲在至資料庫或檔案中，再將這些資料轉檔出來，進行處理。</p>
						</div>
						<div>
							<b>OS</b>
							<p class="textin2">OS 全名為Operating System，作業系統，是管理電腦硬體與軟體資源的電腦程式，同時也是電腦系統的核心與基石。</p>
						</div>
						<div>
							<b>DNS</b>
							<p class="textin2">DNS全名為Domain Name Service，網域名稱系統，是網際網路的一項服務。它作為將域名和IP位址相互對映的一個分散式資料庫，能夠使人更方便地存取網際網路。</p>
						</div>				
						<div>
							<b>OOP</b>
							<p class="textin2">物件導向程式設計（英語：Object-oriented programming，縮寫：OOP）是種具有物件概念的程式編程典範，同時也是一種程式開發的抽象方針。</p>
						</div>
						<div>
							<b>ES6</b>
							<p class="textin2">ES6標準加入了類別、產生器與模組等新功能，讓開發者更加方便，同時也加入詞法作用域（Lexical Block Scoping）以及承諾（Promise）語法讓變數範圍以及運算流程更加明確，不過整體來說，ES6的支援不影響網頁使用者，而是針對後端開發者的更新。</p>
						</div>
					</div>
					<div>
						<h4 id="tag-basic-knowledge"><ins>02:基本知識</ins></h4>
						<p>
							<b>TCP Port</b><a href="http://yun1450.pixnet.net/blog/post/47494172-%E5%B8%B8%E7%94%A8-tcp-port%E4%BD%9C%E7%94%A8%28%E5%90%84%E7%A8%AEport%E4%BB%8B%E7%B4%B9%29" target="_blank">web</a><br>
							<ul>
								<li>ftp，21Port，主要用於FTP，檔傳輸協定服務。</li>
								<li>http，80Port，主要用於www服務上傳輸資訊的協定。</li>
								<li>pop3，110Port，郵件協定3，主要用於接收郵件的。</li>
								<li>https，443Port，即網頁流覽Port</li>
