          <ul>
            <li>
              <pre class="prettyprint">
//npm 安裝 vuetify 與 @babel/polyfill
$npm install -g vuetify @babel/polyfill
//目錄
public
|-index.html
main.js
//index.html，引用 Google icon
&lt;head>
&lt;link
  rel="stylesheet"
  href="https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900|Material+Icons"
/>
&lt;/head>
//main.js，vuetify 相關設定
import '@babel/polyfill' //配合使用 &lt;v-icon&lt;
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css' //使用 vuetufy 的 css 樣式
import colors from 'vuetify/es5/util/colors' //使用 vuetify 的色碼名稱
Vue.use(Vuetify, {
  theme: {
    //自訂所以組件的標準樣式
    primary: colors.red.darken1, // #E53935
    secondary: colors.red.lighten4, // #FFCDD2
    accent: colors.indigo.base, // #3F51B5
    error: '#b71c1c'
  }
})                
