              <p>Props 接收外部傳入的方法:type(String、Object、Function)<br>
                type :表示資料型態 (會進行類型檢測，不符合會在控制台發出警告訊息)<br>
                default:設定預設值 (無傳入資料時，使用預設值)<br>
