//目錄
src
|-components
  |-one_FTC
     |-child1.vue
|-views
  |-one_FTC
    |-father1.vue
    |-father2.vue
App.vue
main.js
router.js
//child1.vue
&lt;template>
  &lt;div id="child1">
    &lt;h3>Child1&lt;/h3>
    getString::${getString} //1-使用 props 的 String
    getObject::
    &lt;div v-for="item in getObject" :key="item.id">${item}}&lt;/div>
    //2-使用 props 的 Object
    &lt;a href="#" @click="getFunction">按鈕&lt;/a> 
    //3-使用 props 的 Function 並在子組件內另外使用 @click 去觸發
  &lt;/div>
&lt;/template>
&lt;script>
  export default {
    name: "child1",
    props: {
      getString: {
        type: String, //1-資料類型 String
        default: "default contry", //當尚未接收資料時，顯示預備字串
        required: false //是否一定要輸入字串，flase 否
      },
      getObject: {
        type: Object, //2-資料類型 Object
        default: function() {
          //當尚未接收資料時，顯示預備物件
          return {
            name: "default name",
            description: "default description",
            price: 300.0
          };
        }
      },
      getFunction: {
        type: Function, //3-資料類型 Function
        required: true //是否一定要輸入函數，true 是
      }
    }
  };
&lt;/script>
&lt;style>
  #child1 {
    background-color: rgb(124, 170, 255);
  }
&lt;/style>
//father.vue
&lt;template>
  &lt;div id="father">
    &lt;h3>Father1&lt;/h3>
    &lt;ul>
      &lt;li>toString::${toString}&lt;/li>
      &lt;li>tObject::${toObject}&lt;/li>
    &lt;/ul>
    &lt;Child1 :getString="toString" :getObject="toObject" :getFunction="toFunction">&lt;/Child1>
    //子組件上掛載 1-String、2-Object、3-Function
  &lt;Child1/div>
&lt;/template>
&lt;script>
import Child1 from "@/components/one_FTC/child1.vue";
export default {
  name: "father1",
  components: {
    Child1
  },
  data() {
    return {
      toString: "Your are Good", //1-設定 String
      toObject: { //2-設定 Object
        name: "big Pen",
        description: "What a nice pen",
        price: 99.12
      }
    };
  },
  methods:{
    toFunction(){ //3-設定 Function
     this.toString = 'Good to you'
    }
  }
};
&lt;/script>
