App.vue
main.js
router.js
//child.vue
&lt;template>
 useString::${useString}
&lt;/template>
&lt;script>
export default {
  name: "child1",
  data() {
    return {
      useString: this.getString
      //將 props 接收的資料另外取名稱，才可在子組件中另外使用
    };
  },
  props: {
    getString: {
      type: String,
      default: "default contry", 
      required: false 
    }
  },
  mounted(){
    this.useString = '123'
  }
};
&lt;/script> 
