            <li>
              <h4>常見應用與API?</h4>
              <p>router.push( location ) 跳轉至指定頁面</p>
              <pre class="prettyprint">
// 情境：登入成功後跳轉頁面
// 來到一個登入頁面組件 login.vue
// 透過 template 中的登入按鈕觸發 router.push( location )
//login.vue
&lt;template>
&lt;button @click="login">login&lt;/button>
&lt;/template>
&lt;script>
export default {
  methods: {
    login(){
      this.$http.post(api, this.user)
        .then( ({data}) => {
          // 以上都不是重點，重點在此
          this.$router.push('/')
          // 透過此方法，可以直接跳轉到根目錄
        })
    }
  }
}
&lt;/script></pre>
