              <h4>vue-router 模式的設計</h4>
              <pre class="prettyprint">
// 來到我們管理路由的檔案router.js
export default new VueRouter({
  mode: 'history',  //若不寫mode就預設為hash模式
  routes: [
    ......
  ]
})</pre>
              <p>如果省略井字號，就是 history 模式。該模式乾淨整潔，但是代價為後端伺服器必須設置 URL Rewrite 重新將路徑指向 index.html ( SPA只在此頁 )。若後端配合好，則 history 模式當然會比較好看，後端無法配合則需採用 hash 模式。<mark>注意：設定為 history 模式，在開發測試環境會自動幫你做到 URL Rewrite，實際上線版本還是要再做設定的不要忘記</mark></p>
            </li>
