              <h4>轉址與別名的設計?</h4>
              <p>轉址，當匹配了該路徑，會自動'轉換'至指定的路徑。 路由設置中，藉由redirect屬性來達成轉址</p>
              <pre class="prettyprint">
//router.js
// 轉址設定為獨立物件，表示匹配該路徑時，會自動跳轉到另外指定的路徑，路徑也會跟著變換
{
  path: '/aboutus',
  redirect: '/About/us'
}
// 若連結輸入/aboutus
// 自動跳轉至/About/us路由頁面</pre>
