//router.js
{
  path: '/About',
  component: About,
  children: [
    {
      path: 'WTAPS',
      component: WTAPS
    },
    {
      path: 'NBHD',
      component: NBHD
    },
  ]
}
// 此時連結為#/About/WTAPS時
// router-view就會匹配顯示WTAPS組件</pre>
