router.js            
//router.js
import Vue from 'vue'
import Router from 'vue-router'
export default new Router({
routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    }
]
)}
//方式一，使用 path 路徑
//App.vue
&lt;router-link to="/">hone&lt;/router-link>
//方式二，使用 name 為路徑
&lt;router-link :to="{ name: 'home'}">User&lt;/router-link>
//使用在 router.js 內的自訂名稱;名稱就像是 ID 只有唯一性，不可有重複的 name 取名名稱。</pre>
