//main.js，設定引用套件與建立 Vue 實例
import Vue from 'vue'
import router from './router' //使用自訂 router
import App from './App'

Vue.config.productionTip = false 
//設定 false 以阻止 vue 在啟動時生成生產提示

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
//Home.vue，設定 view 組件畫面
&lt;template>
  &lt;div>
    &lt;h3>Home&lt;/h3>
  &lt;/div>
&lt;/template>
&lt;script>
  export default {
    name: "Home"
  };
&lt;/script>
&lt;style>&lt;/style>
//router.js，設定 router 路徑
import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
  ]
})
//App.vue，設定進入組件
&lt;template>
  &lt;div>
    &lt;h3>App.vue&lt;/h3>
    &lt;div class="container">
      &lt;router-link :to="/">Home&lt;/router-link> //使用vue-router 的路徑
      &lt;Home>&lt;/Home> //引用自訂組件
      &lt;router-view>&lt;/router-view> //顯示 router-link 進來的組件畫面
    &lt;/div>
  &lt;/div>
&lt;/template>
&lt;script>
  import Home from "@/views/Home.vue";
  export default {
    name: "App",
    component: {
      Home
    }
  };
&lt;/script>                
              </pre>
