<template>
  <div v-cloak style="dispaly:position:absolute;width:100vw">
    <main>
      <transition
        enter-active-class="point-enter-active"
        leave-active-class="point-leave-active"
      >
        <section v-show="pointShow" class="point">
          <div>{{ point }}</div>
        </section>         
      </transition>
      <transition
        enter-active-class="window-enter-active"
        leave-active-class="window-leave-active"
      >
        <section v-show="pindexWindow" class="window">       
          <div>
            <p>{{ winodwPoint }}</p>
            <button @click="checkWindow(1)">確定</button>      
            <button @click="checkWindow(2)">取消</button>
          </div>    
        </section> 
      </transition>
      <transition
        enter-active-class="window-enter-active"
        leave-active-class="window-leave-active"
      >
        <section v-show="cindexWindow" class="window">
          <div>
            <p>{{ winodwPoint }}</p>
            <button @click="checkWindow(3)">確定</button>
            <button @click="checkWindow(4)">取消</button>
          </div>
        </section>
      </transition>        
      <transition
        enter-active-class="bullet-enter-active"
        leave-active-class="bullet-leave-active"
      >
        <section v-if="!groupShow">
          <div class="btn" v-if="logingPage">
            <button @click="changePage">開始註冊</button>
          </div>
          <div class="btn" v-if="registeredPage">
            <button @click="changePage">開始登入</button>
          </div>
        </section>
      </transition>
      <transition
        enter-active-class="bullet-enter-active"
        leave-active-class="bullet-leave-active"
      >
        <section style="z-index:10" v-if="!groupShow">
          <transition
            enter-active-class="fade-enter-active"
            leave-active-class="fade-leave-active"
          >
            <section class="login" v-if="logingPage">
              <div>
                <h1>登入</h1>
                <span>Login</span>
              </div>
              <div>
                <table class="table">
                  <tr>
                    <td><label for="email">信箱</label></td>
                    <td>
                      <input
                        type="email"
                        v-model="Lemail"
                        placeholder=" Email"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td><label for="password">密碼</label></td>
                    <td>
                      <input
                        type="text"
                        v-model="Lpassword"
                        placeholder=" Password"
                      />
                    </td>
                  </tr>
                </table>
              </div>
              <div>
                <button @click="loginSubmit">登入</button>
              </div>
              <div>
                <p>
                  你還沒註冊嗎?<br />趕緊註冊一個讓你有個隨時筆記的子彈筆記吧!
                </p>
                <span class="forgetPasswordBtn" @click="toforgetPassword(1)">忘記密碼?</span>
              </div>
            </section>
          </transition>
          <transition 
            enter-active-class="fade-enter-active"
            leave-active-class="fade-leave-active">
            <section class="forgetPassWord" v-if="forgetPasswordPage">
              <div>
                <h1>重新註冊密碼</h1>
                <span>Re-enroll Password</span>
              </div>  
              <div>
                <table>
                  <tr>
                    <td><label for="femail">信箱</label></td>
                    <td><input type="text" v-model="femail"></td>
                  </tr>
                  <tr>
                    <td><label for="fnickname">暱稱</label></td>
                    <td><input type="text" v-model="fnickname"></td>
                  </tr>
                  <tr>
                    <td><label for="fpassword">密碼</label></td>
                    <td><input type="text" v-model="fpassword"></td>
                  </tr>
                  <tr>
                     <td><label for="fcheckPassword">確認密碼</label></td>
                    <td><input type="text" v-model="fcheckPassword"></td>
                  </tr>
                </table>
              </div>
              <div>
                <button @click="forgetPasswordSubmit">送出</button>
              </div>
              <div>
                <button @click="toforgetPassword(2)">返回登入</button>
              </div>
            </section>
          </transition>
          <transition
            enter-active-class="fade-enter-active"
            leave-active-class="fade-leave-active"
          >
            <section class="registered" v-if="registeredPage">
              <div>
                <h1>註冊</h1>
                <span>Registered</span>
              </div>
              <div>
                <table class="table">
                  <tr>
                    <td><label for="email">信箱</label></td>
                    <td>
                      <input
                        type="email"
                        v-model="Remail"
                        placeholder=" Email"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td><label for="password">密碼</label></td>
                    <td>
                      <input
                        type="text"
                        v-model="Rpassword"
                        placeholder=" Password"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td><label for="password">確認密碼</label></td>
                    <td>
                      <input
                        type="text"
                        v-model="RcheckPassword"
                        placeholder=" Check Password"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td><label for="nickname">暱稱</label></td>
                    <td>
                      <input
                        type="text"
                        v-model="Rnickname"
                        placeholder=" Nickname"
                      />
                    </td>
                  </tr>
                  <tr class="tableEx">
                    <td></td>
                    <td>
                      請記住您輸入的暱稱，以作為忘記密碼時的資料認證!
                    </td>
                  </tr>
                </table>
              </div>
              <div>
                <button @click="registeredSubmit">註冊</button>
              </div>
              <div>
                <p>註冊完成後來登入去體驗子彈筆記</p>
              </div>
            </section>
          </transition>
        </section>
      </transition>      
      <transition
        enter-active-class="bullet-enter-active"
        leave-active-class="bullet-leave-active"
      >
        <section v-if="groupShow">
          <section>
            <button @click="outSubmit" class="outSubmit">登出</button>
          </section>
          <section class="title"><h1>子彈筆記</h1></section>
          <h2 class="titleTwo">新增群組</h2>
          <section class="add">
            <div>
              <label for="">群組名稱:</label>
              <input
                type="text"
                placeholder="Group Name"
                v-model="groupName"
              />
              <label for="">群組說明:</label>
              <input
                type="text"
                placeholder="Group Name Description"
                v-model="groupNameDescription"
              />
              <button @click="addRow">新增群組</button>
            </div>
          </section>
          <h2 class="titleTwo">編輯群組</h2>
          <section>
            <div v-for="(item,zindex) in newGroup" :key="zindex">
              <section                  
                v-for="(item, pindex) in item.groupList"
                :key="pindex"
                class="edit"
              >
                <section>
                  <div>
                    <h2
                      class="editGroup"
                      @click="editGroupName(pindex)"
                    >
                      {{ item.groupName }}
                    </h2>
                    <input
                      type="text"
                      v-if="item.groupNameShow"
                      v-model="item.groupName"
                    />
                    <span @click="editDescription(pindex)" class="editGroup">
                      {{ item.groupNameDescription }}
                    </span>
                    <input
                    type="text" v-if="item.descriptionShow" v-model="item.groupNameDescription" />
                    <span class="delectGroup" @click="delectPindex(pindex)">刪除</span>
                  </div>
                   <button @click="toggleShow(pindex)">{{ item.point }}</button>
                </section>
                <hr>
                <section v-show="item.show" class="bStyle">
                  <div>
                    <section>
                      <button @click="orderPage(pindex)">排序頁數</button>
                      <button @click="addCindex(pindex)">{{ item.cindexPoint }}</button>
                      <button @click="clearnAddCindex(pindex)" v-show="item.cindexShow">
                        取消
                      </button>
                    </section>
                    <section v-show="item.cindexShow">
                      <div>
                        <label for="page">頁數</label>
                        <input type="number" v-model="page" placeholder="Page" />
                      </div>
                      <div>
                        <label for="content">內容</label>
                        <textarea type="text" v-model="content" placeholder="Content"></textarea>
                      </div>
                    </section>
                  </div>
                  <div class="aStyle">
                    <section v-for="(item,cindex) in item.items" :key="cindex">
                      <div>
                        <section>
                          <button @click="toggleEdit(cindex,pindex)">
                            {{ item.point }}
                          </button>
                          <button
                            @click="delectCindex(cindex,pindex)"
                            :class="{'toEdit':item.show}"
                          >
                            ✘
                          </button>
                        </section>
                         <pre :class="{'toEdit':item.show}">{{ item.content }}</pre>
                         <h4 :class="{'toEdit':item.show}">{{ item.page }}</h4>
                      </div>
                      <transition
                        enter-active-class="box-enter-active"
                        leave-active-class="box-leave-active"
                      >
                        <div v-show="item.show">
                          <textarea rows="10" v-model="item.content"></textarea>
                          <input type="text" v-model="item.page" />
                        </div>
                      </transition>
                    </section>
                  </div>
                </section>
              </section>
            </div>
          </section>         
        </section>
      </transition>
    </main>
    <footer>
      <p>beat 版本</p>
    </footer>
  </div>
</template>

<script>
  export default {
    name: "bullet",
    data() {
          return {
            //通用
            point: '',
            winodwPoint: '',
            pointShow: false,
            logingPage: true,
            registeredPage: false,
            forgetPasswordPage:false,
            outTime: '',
            //登入 login
            Lemail: 'test@gmail.com',
            Lpassword: 'test',
            includesEmail: '',
            uersIndex: '',
            groupIndexL: '',
            getUPassword: '',
            getUId: '',
            //忘記密碼 forgetPassword
            femail:'',
            fnickname:'',
            fpassword:'',
            fcheckPassword:'',
            forgetPasswordIndex:'',
            newForgetPassword:[],
            fid:'',
            //註冊 Registered
            Remail: '',
            Rpassword: '',
            RcheckPassword: '',
            Rnickname: '',
            key: '',
            id: '',
            newRegistered: [],
            users: [],
            //子彈 bullet
            groupShow: false,
            pindexWindow: false,
            cindexWindow: false,
            groupName: '',
            groupNameDescription: '',
            page: '',
            content: '',
            rows: [], //暫存 pindex 資料
            rowsCindex: [], //暫存 cindex 資料
            pIndex: 0, //當前第一層 index
            cIndex: 0, //當前第二層 index
            newGroup: [
              {
                id: 101,
                groupList: [
                  {
                    groupName: '旅遊',
                    groupNameDescription: '外出的活動',
                    cindexPoint: '新增',
                    point: '隱藏',
                    groupNameShow: false,
                    descriptionShow: false,
                    cindexShow: false,
                    show: true,
                    items: [
                      {
                        content: '準備東部旅遊計畫',
                        page: 1,
                        show: false,
                        point: '✍🏻'
                      },
                      {
                        content: '準備西部旅遊計畫',
                        page: 2,
                        show: false,
                        point: '✍🏻'
                      },
                      {
                        content: '準備台南',
                        page: 9,
                        show: false,
                        point: '✍🏻'
                      },
                      { content: '準備嘉義', page: 4, show: false, point: '✍🏻' }
                    ]
                  }
                ]
              }
            ], //當前使用者資料
            group: [
              {
                id: 109,
                groupList: [
                  {
                    groupname: '旅行',
                    items: [
                      {
                        title: '北海岸',
                        page: 1
                      }
                    ]
                  }
                ]
              },
              {
                id: 100,
                groupList: [
                  {
                    groupName: '旅遊',
                    groupNameDescription: '外出的活動',
                    cindexPoint: '新增',
                    point: '隱藏',
                    groupNameShow: false,
                    descriptionShow: false,
                    cindexShow: false,
                    show: true,
                    items: [
                      {
                        content: '準備東部旅遊計畫',
                        page: 1,
                        show: false,
                        point: '✍🏻'
                      },
                      {
                        content: '準備西部旅遊計畫',
                        page: 2,
                        show: false,
                        point: '✍🏻'
                      },
                      {
                        content: '準備台南',
                        page: 9,
                        show: false,
                        point: '✍🏻'
                      },
                      { content: '準備嘉義', page: 4, show: false, point: '✍🏻' }
                    ]
                  },
                  {
                    groupName: '隨手筆記',
                    groupNameDescription: '隨手紀錄想到的事物',
                    cindexPoint: '新增',
                    point: '隱藏',
                    groupNameShow: false,
                    descriptionShow: false,
                    cindexShow: false,
                    show: true,
                    items: [
                      {
                        content: '準備東部旅遊計畫',
                        page: 1,
                        show: false,
                        point: '✍🏻'
                      },
                      {
                        content: '準備西部旅遊計畫',
                        page: 2,
                        show: false,
                        point: '✍🏻'
                      },
                      {
                        content: '準備台南',
                        page: 9,
                        show: false,
                        point: '✍🏻'
                      },
                      { content: '準備嘉義', page: 4, show: false, point: '✍🏻' }
                    ]
                  },
                  {
                    groupName: '計畫',
                    groupNameDescription: '生活計畫、基金規劃',
                    cindexPoint: '新增',
                    point: '隱藏',
                    groupNameShow: false,
                    descriptionShow: false,
                    cindexShow: false,
                    show: true,
                    items: [
                      {
                        content: '準備東部旅遊計畫',
                        page: 1,
                        show: false,
                        point: '✍🏻'
                      }
                    ]
                  }
                ]
              },
              {
                id: 101,
                groupList: [
                  {
                    groupname: '旅行',
                    items: [
                      {
                        title: '北海岸',
                        page: 1
                      }
                    ]
                  }
                ]
              }
            ]
          }
        },
        methods: {
          //登入 login
          async feachUsers() {
            try {
              const instance = axios.create({
                timeout: 6000,
                headers: {
                  'secret-key':
                    '$2a$10$XflNqIVbG42ADERhOMsvp.KChlqUCodB/fEQFGSdjmIfKOqonOJci'
                }
              })
              const get = await instance.get(
                'https://api.jsonbin.io/b/5d4ffd90f090a433857d1d83'
              )
              const { data } = get //資料在 data 屬性
              this.users = data.users //將GET的資料放到Vue上的groupList陣列中
            } catch (error) {
              throw new Error(error)
            }
          },
          async putUser() {
            try {
              const instance = axios.create({
                timeout: 6000,
                headers: {
                  'secret-key':
                    '$2a$10$XflNqIVbG42ADERhOMsvp.KChlqUCodB/fEQFGSdjmIfKOqonOJci',
                  'Content-Type': 'application/json',
                  versioning:false
                }
              })
              let data = this.users
              const put = await instance.put(
                'https://api.jsonbin.io/b/5d4ffd90f090a433857d1d83',
                { users: data },
                this.seccessPutUser()
              )
            } catch (error) {
              throw new Error(error)
            }
          },
          seccessPutUser() {
            this.changePage(1)
            this.showPoint('註冊成功')
            
          },
          changePage(m) {
            if(m==1){
              this.logingPage = !this.logingPage
              this.registeredPage = !this.registeredPage
              this.Lemail = this.Remail
              this.Lpassword = this.Rpassword
            }else{
              this.logingPage = !this.logingPage
              this.registeredPage = !this.registeredPage
              this.pointShow = false
              this.Remail = ''
              this.Rpassword = ''
              this.RcheckPassword = ''
              this.Rnickname = ''
            }
          },
          loginSubmit() {
            let checkEmpty = is.any.empty(this.Lemail, this.Lpassword)
            let checkEmail = is.not.email(this.Lemail)
            let checkAbove = is.above(4, this.Lpassword.length)
            if (checkEmpty) {
              this.showPoint('資料尚未填寫完整')
            } else if (checkEmail) {
              this.showPoint('信箱輸入格式錯誤')
              this.Remail = ''
            } else if (checkAbove) {
              this.showPoint('密碼請輸入四(含)個以上')
            } else {
              let resultEmail = this.users.map(function(item) {
                return item.email
              })
              this.includesEmail = resultEmail.includes(this.Lemail)
              if (this.includesEmail) {
                this.checkUIndex()
                this.checkUPassword()
              } else {
                this.showPoint('無此信箱帳號')
                this.Lemail = ''
              }
            }
          },
          checkUIndex() {
            //回傳 U index
            this.usersIndex = this.users
              .map(function(item) {
                return item.email
              })
              .indexOf(this.Lemail)
          },
          checkUPassword() {
            //回傳 U password 位置
            this.getUPassword = this.users[this.usersIndex].key
            let salt1 = 'sd132410sdd-16dsd21-4ds545-BdsSD23AE3-32998d1227D3'
            let salt2 = 'sd1324-45-BdDsdsSDAE3d-sad3-98ddsd27D3'
            let key = md5(md5(salt1 + this.Lpassword) + salt2)
            this.key = key
            if (key == this.getUPassword) {
              this.checkUId()
              this.checkGIndex()
              this.filterGroup()
            } else {
              this.showPoint('密碼輸入錯誤')
            }
          },
          checkUId() {
            this.getUId = this.users[this.usersIndex].id
          },
          checkGIndex() {
            //回傳 G Index
            this.groupIndex = this.group
              .map(function(item) {
                return item.id
              })
              .indexOf(this.getUId)
          },
          filterGroup() {
            this.newGroup = [this.group[this.groupIndex]]
            this.Lemail = ''
            this.Lpassword = ''
            this.groupShow = true
            this.showPoint('登入中')
            this.loginMessage()
          },
          //註冊 Registered
          registeredSubmit() {
            let checkEmpty = is.any.empty(
              this.Remail,
              this.Rpassword,
              this.RcheckPassword,
              this.Rnickname
            )
            let checkEmail = is.not.email(this.Remail)
            let checkAbove = is.above(4, this.Rpassword.length)
            let checkEqual = is.not.equal(this.Rpassword, this.RcheckPassword)
            if (checkEmpty) {
              this.showPoint('資料尚未填寫完整')
            } else if (checkEmail) {
              this.showPoint('信箱輸入格式錯誤')
              this.Remail = ''
            } else if (checkAbove) {
              this.showPoint('密碼請輸入四(含)個以上')
            } else if (checkEqual) {
              this.showPoint('密碼與確認密碼不相同')
              this.Rpassword = ''
              this.RcheckPassword = ''
            } else {
              for (i = 0; i < this.users.length; i++) {
                if (this.Remail == this.users[i].email) {
                  this.showPoint('此信箱已註冊')
                  this.Remail = ''
                  break
                } else {
                  this.point = '註冊中...'
                  this.pointShow = true
                  let salt1 =
                    'sd132410sdd-16dsd21-4ds545-BdsSD23AE3-32998d1227D3'
                  let salt2 = 'sd1324-45-BdDsdsSDAE3d-sad3-98ddsd27D3'
                  let key = md5(md5(salt1 + this.Rpassword) + salt2)
                  this.key = key
                }
              }
              if (this.Remail != '') {
                this.addnewRegistered()
                this.putUser()
              }
            }
          },
          addnewRegistered() {
            const vm = this
            this.setId()
            vm.newRegistered = {
              id: this.id,
              key: this.key,
              email: this.Remail,
              nickname: this.Rnickname
            }
            vm.users.push(vm.newRegistered)
          },
          setId() {
            let length = this.users.length
            let id = 100 + length
            this.id = id
          },
          //忘記密碼 forgetPassword
          toforgetPassword(c){
            if(c==1){
             this.forgetPasswordPage = true
             this.logingPage = false
            }else{
              this.forgetPasswordPage = false
              this.logingPage = true
              this.registeredPage = false
            }
          },
          forgetPasswordSubmit(){
            let checkEmpty = is.any.empty(
              this.fnickname,
              this.femail,
              this.fpassword,
              this.fcheckPassword
            )
            if(checkEmpty){
              this.showPoint('資料尚未填寫完整')
            }else{
              let resultEmail = this.users.map(function(item){
                return item.email
              })             
              let includesEmail = resultEmail.includes(this.femail)
              if(includesEmail){
                this.forgetPasswordIndex = resultEmail.indexOf(this.femail)
                this.newForgetPassword = this.users[this.forgetPasswordIndex]
                this.checkFnickname()
              }else{  
                this.showPoint('無此信箱帳號')
                this.femail = ''                
              }
            }
          },
          checkFnickname(){
            let nickname = this.newForgetPassword.nickname
            let checkEqual = is.not.equal(nickname, this.fnickname)
            if(checkEqual){
              this.showPoint('無此暱稱')
              this.fnickname = ''
            }else{
              let checkAbove = is.above(4,this.fpassword.length)
              let checkEqual = is.not.equal(this.fpassword,this.fcheckPassword)
              if(checkAbove){
                this.showPoint('密碼請輸入四(含)個以上')
              }else if(checkEqual){
               this.showPoint('密碼與確認密碼不相同')
               this.fpassword = ''
               this.fcheckPassword = ''
              }else{
                this.feachUsers()
                let salt1 =
                    'sd132410sdd-16dsd21-4ds545-BdsSD23AE3-32998d1227D3'
                let salt2 = 'sd1324-45-BdDsdsSDAE3d-sad3-98ddsd27D3'
                let key = md5(md5(salt1 + this.fpassword) + salt2)
                this.newForgetPassword.key = key
                this.users[this.forgetPasswordIndex].key = key          
                this.putUser()                
                this.femail = ''
                this.fnickname = ''
                this.fpassword = ''
                this.fcheckPassword = ''
                this.toforgetPassword(2)               
                this.showPoint('密碼變更完成')
              }
            }
          },
          //通用
          showPoint(message) {
            this.point = message
            this.pointShow = true
            this.clearTime()
            this.setTime()
          }, //使用
          setTime() {
            this.time = setTimeout(() => (this.pointShow = false), 3000)
          }, //使用
          clearTime() {
            if (this.time) {
              clearInterval(this.time)
            }
          },
          pointWindow() {
            this.isShow = !this.show
          },
          windowPoint(f) {
            if (f == 1) {
              this.pindexWindow = true
              this.cindexWindow = false
            } else {
              this.cindexWindow = true
              this.pindexWindow = false
            }
          },
          checkWindow(f) {
            let p = this.pindex
            let c = this.cindex
            let g = this.newGroup[0].groupList  
            if (f == 1) {               
              this.pindexWindow = false
              this.showPoint('已刪除群組')
              g.splice(p, 1)
            } else if (f == 2) {
              this.pindexWindow = false
              this.showPoint('保留群組')
            } else if (f == 3) {
              this.cindexWindow = false
              this.showPoint('已刪除頁面')
              g[p].items.splice(c, 1)
            } else {
              this.cindexWindow = false
              this.showPoint('保留頁面')
            }
          },
          //子彈 bullet
          loginMessage() {
            let vm = this
            vm.outTime = setTimeout(() => {
              this.showPoint('即將在 3 分鐘後自動登出')
              this.loginTime()
            }, 720000)
          },
          loginTime() {
            let vm = this
            vm.outTime = setTimeout(() => {
              this.groupShow = false
              this.showPoint('自動登出')
            }, 360000)
          },
          outSubmit() {
            this.groupShow = false
            this.showPoint('已登出')
            if (this.outTime) {
              clearInterval(this.outTime)
            }
          },
          //子彈 bullet - 新增
          addRow() {
            if (this.groupName == '') {
              this.showPoint('請輸入完整的__群組名稱')
            } else if (this.groupNameDescription == '') {
              this.showPoint('請輸入完整的__群組說明')
            } else {
              this.point = ''
              this.pointShow = false
              const vm = this
              vm.rows = {
                groupName: this.groupName,
                groupNameDescription: this.groupNameDescription,
                cindexPoint: '新增',
                point: '隱藏',
                groupNameShow: false,
                descriptionShow: false,
                cindexShow: false,
                show: true,
                items: [
                  {
                    content: '預設內容',
                    page: 1,
                    show: false,
                    point: '✍🏻'
                  }
                ]
              }
              vm.newGroup[0].groupList.unshift(vm.rows)
              vm.groupName = ''
              vm.groupNameDescription = ''
              vm.point = ''
            }
          },
          //子彈 bullet - 編輯
          editGroupName(pindex) {
            let g = this.newGroup[0].groupList
            let p = pindex
            g[p].groupNameShow = !g[p].groupNameShow
            if (g[p].groupNameShow == true) {
              this.showPoint('編輯群組名稱中')
            }
          },
          editDescription(pindex) {
            let g = this.newGroup[0].groupList
            let p = pindex
            g[p].descriptionShow = !g[p].descriptionShow
            if (g[p].descriptionShow == true) {
              this.showPoint('編輯群組說明中')
            }
          },
          delectPindex(pindex) {
            this.pindex = pindex
            this.winodwPoint = `
            確定要刪除當前群組
            刪除群組將不會留存任何資料`
            this.windowPoint(1)
          },
          toggleShow(index) {
            let g = this.newGroup[0].groupList
            let p = (this.pIndex = index)
            let c = this.cIndex
            g[p].show = !g[p].show
            g[p].groupNameShow = false
            g[p].descriptionShow = false         
            if (g[p].show == true) {
              g[p].point = '隱藏'
              for (i = 0; i < g[p].items.length; i++) {
                g[p].items[i].point = '✍🏻'
                g[p].items[i].show = false
              }
              return g[p].items.sort(comparePage) //排序當前的 page
            } else {
              g[p].point = '顯示'
            }           
          },
          orderPage(index) {
            let p = (this.pIndex = index)
            let g = this.newGroup[0].groupList
            return g[p].items.sort(comparePage)
          },
          addCindex(pindex) {
            let p = pindex
            let g = this.newGroup[0].groupList
            let cindexLength = g[p].items.length
            if (g[p].cindexShow == false) {
              g[p].cindexShow = true
              g[p].cindexPoint = '確認'
            } else {
              if (this.content == '') {
                this.showPoint('請輸入內容')
              } else if (this.page == '') {
                this.page = 1
                this.showPoint('頁數自動預設為 1')
              }else if(this.page > 99){
                this.showPoint('請輸入1 - 99 頁數')
              }else {
                const vm = this
                vm.rowsCindex = {
                  content: this.content,
                  page: this.page,
                  show: false,
                  point: '✍🏻'
                }           
                vm.newGroup[0].groupList[p].items.unshift(vm.rowsCindex)
                vm.content = ''
                vm.page = ''
                this.showPoint('已新增新頁數內容')
                g[p].cindexShow = false
                g[p].cindexPoint = '新增'
              }
            }
          },
          clearnAddCindex(pindex) {
            let p = pindex
            let g = this.newGroup[0].groupList
            g[p].cindexShow = false
            g[p].cindexPoint = '編輯'
            this.showPoint('取消編輯')
          },
          toggleEdit(cindex, pindex) {
            // 測試當前階層，console.log(`當前父層${p}、當前子層${c}`)
            let g = this.newGroup[0].groupList
            let c = (this.cIndex = cindex)
            let p = (this.pIndex = pindex)
            g[p].items[c].show = !g[p].items[c].show
            if (g[p].items[c].show == true) {
              g[p].items[c].point = '✔'
            } else {
              g[p].items[c].point = '✍🏻'
              this.showPoint('完成頁面編輯')
            }
            /*
            includes 陣列判斷
            let pc = String(p) + String(c)
            if (this.action.includes(pc)) {
            } else {
              this.action.push(pc)
            }*/
          },
          delectCindex(cindex, pindex) {
            this.cindex = cindex
            this.pindex = pindex
            this.winodwPoint = `是否要刪除當前頁數`
            this.windowPoint(2)
          }
        }, 
        mounted() {
          this.feachUsers()
          this.comparePage = function comparePage(a, b) {
            if (a.page   < b.page) return -1
            if (a.name > b.page) return 1
            return 0
          }
        }    
  };
</script>

