//base
import Vue from 'vue'
import Router from 'vue-router'

//use views
import Home from '@/views/Home'

//backEnd
import php from '@/views/backEnd/php'
import smart from '@/views/backEnd/smart'

// others
import WBS from '@/views/others/wbs'
import allFlowChart from '@/views/others/allFlowChart'
import tool from '@/views/others/tool'
import webTool from '@/views/others/webTool'

//test
import projectOne from '@/views/test/projectOne'
import father from '@/views/test/father'
import bullet from '@/views/test/bullet'

//instruction
import ES6 from '@/views/instruction/es6'
import Sass from '@/views/instruction/sass'

//collection
import aboutMe from '@/views/collection/aboutMe'
import es6 from '@/views/collection/es6'
import git from '@/views/collection/git'
import museUi from '@/views/collection/museUi'
import sass from '@/views/collection/sass'
import vue from '@/views/collection/vue'
import vuex from '@/views/collection/vuex'
import vuetify from '@/views/collection/vuetify'
import webpack from '@/views/collection/webpack'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  srcollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/backend/php',
      name: 'PHP',
      component: php
    },
    {
      path: '/backend/smart',
      name: 'Smart',
      component: smart
    },
    {
      path: '/others/wbs',
      name: 'WBS',
      component: WBS
    },
    {
      path: '/others/allflowchart',
      name: 'allFlowChart',
      component: allFlowChart
    },
    {
      path: '/others/tool',
      name: 'tool',
      component: tool
    },
    {
      path: '/others/webTool',
      name: 'webTool',
      component: webTool
    },
    {
      path: '/test/projectone',
      name: 'projectOne',
      component: projectOne
    },
    { path: '/test/father', name: 'father', component: father },
    { path: '/test/bullet', name: 'bullet', component: bullet },
    {
      path: '/instruction/es6',
      name: 'ES6',
      component: ES6
    },
    {
      path: '/instruction/sass',
      name: 'Sass',
      component: Sass
    },
    {
      path: '/collection/aboutMe',
      name: 'aboutMe',
      component: aboutMe
    },
    {
      path: '/collection/es6',
      name: 'es6',
      component: es6
    },
    {
      path: '/collection/git',
      name: 'git',
      component: git
    },
    {
      path: '/collection/museui',
      name: 'museUi',
      component: museUi
    },
    {
      path: '/collection/sass',
      name: 'sass',
      component: sass
    },
    {
      path: '/collection/vue',
      name: 'vue',
      component: vue
    },
    {
      path: '/collection/vuex',
      name: 'vuex',
      component: vuex
    },
    {
      path: '/collection/vuetify',
      name: 'vuetify',
      component: vuetify
    },
    {
      path: '/collection/webpack',
      name: 'webpack',
      component: webpack
    }
  ]
})
