<template>
  <div class="viewWidth">
    <div class="center">
      <div class="title">
        <h1>Css/Scss</h1>
        <p>CSS Extended language</p>
        <a href=""></a>
      </div>
      <div class="directory">
        <h2>目錄</h2>
        <ul>
          <li>如何設定 prettify 顯示程式碼的區塊?</li>
          <li>如何在 vue-cli 環境中使用 SCSS ?</li>
          <li>如何引用 CSS/SCSS ?</li>
        </ul>
        <hr>
      </div>
      <!-- li>h3+(ul>li>pre.prettyprint) -->
      <ul>
        <li>
          <h3>如何設定 prettify 顯示程式碼的區塊?</h3>
          <ul>
            <li>
              <pre class="prettyprint">
//目錄
public
|-index.html
src
|-views
  |-Home.vue
style
|-pretty.scss
//index.html
&lt;head>
&lt;script src="https://cdn.jsdelivr.net/gh/google/code-prettify
@master/loader/run_prettify.js">&lt;/script>
&lt;/head>
//Home.vue
&lt;template>
  &lt;pre class="prettyprint">
    //撰寫 coding..
  &lt;/pre>
&lt;/template>
&lt;script>
export default({
  name:'Home'
})
&lt;/script>
&lt;style lang="scss" scoped>
  @import "../../style/pretty.scss";
&lt;/style>
              </pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>如何在 vue-cli 環境中使用 SCSS ?</h3><a
            href="https://bingzhe.github.io/2017/06/22/vue-cli-%E4%B8%AD%E4%BD%BF%E7%94%A8sass/"
            target="_blank"
          >vue-cli中使用Scss</a>
          <ul>
            <li>
              <pre class="prettyprint">
//目錄
style
|-common.scss
scr
|-views
  |-aComponent.vue
//安裝編譯 SCSS 需要的 loader
//由於使用 vue-cli 所以省略 loader 的配置
$ npm install sass-loader node-sass --save
//common.scss，自訂通用的SCSS 檔案
@mixin backgroundStyle(
    $background-color,
    $display,
    $justify-content,
    $flex-wrap
  ) {
    background-color: $background-color;
    display: $display;
    justify-content: $justify-content;
    flex-wrap: $flex-wrap;
  }
//aComponent.vue
//在組件中匯入通用的SCSS 檔案
//lang 為定義 style 的內使用的語言，scopes 為定義此 style 僅限在這一份組件當中使用
&lt;style lang="scss" scoped>
  @import "../../style/common.scss";
  #test {
    width: 100vh;
       @include backgroundStyle(#fff, flex, center, wrap);
  }
&lt;/style></pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>如何引用 CSS/SCSS ?</h3>
          <ul>
            <li>
              <pre class="prettyprint">
//目錄
src
|-main.js
style
|-style.scss
//main.js，引用 SCSS
import '../style/style.scss'
//style.scss，設定樣式
.container { 
	background-color:bisque
}</pre>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "git"
  };
</script>

<style lang="scss" scoped>
  @import "../../../style/common.scss";
  @import "../../../style/pretty.scss";
  @import "../../../style/style.scss";
</style>
