<template>
  <div id="father">
    <h3>Father</h3>
    <ChildComponent
      @created="handleCreate"
      @send-message="handleSendMessage"
    /> <!-- 將方法放到子內-->
  </div>
</template>

<script>
  import ChildComponent from "./child";
  export default {
    name: "father",
    components: {
      ChildComponent
    },
    data() {
      return {
        val: "hahaha"
      };
    },
    methods: {
      handleCreate() {
        console.log("Child has been created");
      },
      handleSendMessage(event, val) {
        console.log("From the child", val);
      }
    }
  };
</script>

<style scoped>
  #father {
    width: 60%;
    background-color: orange;
  }
  ul {
    list-style: none;
  }
</style>
