<template>
  <div style="margin:0 -2rem 0 -2rem;">
    <ul class="exList">
      <li
        v-for="(val,key) in getList"
        :key="key"
      >
        <p v-if="typeof val!='object'">{{ val }}</p>
        <p v-else>{{ key }}</p>
        <ul
          v-if="typeof val=='object'"
          class="exContant"
        >
          <li
            v-for="(val,key) in val"
            :key="key"
          >
            <a
              :href="val.url"
              target="_blank"
            >{{ val.content }}</a>
            <span>{{ val.label }}</span>
            <p>{{ val.situation }}</p>
            <p>{{ val.contentText }}</p>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "example",
    props: {
      getList: {
        type: Object
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import "../../style/common.scss";
  @import "../../style/style.scss";
  .exList {
    background-color: rgb(48, 48, 48);
    color: #fff;
    //font-family: 微軟正黑體;
    margin: 0;
    padding: 0;

    ul {
      list-style: none;
      margin-left: -2.5rem;
    }
    a {
      background-color: rgb(177, 47, 0);
      color: #fff;
      padding: 0.15rem;
      border-radius: 5px;
      text-decoration: none;
    }
    li {
      p {
        padding: 0.5rem;
      }
    }
  }

  .exList > li:nth-of-type(1) > p,
  .exList > li:nth-of-type(1) a {
    background-color: rgb(211, 66, 66);
  }

  .exList > li:nth-of-type(2) > p,
  .exList > li:nth-of-type(2) a {
    background-color: rgb(211, 95, 66);
  }

  .exList > li:nth-of-type(3) > p,
  .exList > li:nth-of-type(3) a {
    background-color: rgb(211, 148, 66);
  }

  .exList > li:nth-of-type(4) > p,
  .exList > li:nth-of-type(4) a {
    background-color: rgb(143, 211, 66);
  }

  .exList > li:nth-of-type(5) > p,
  .exList > li:nth-of-type(5) a {
    background-color: rgb(66, 211, 167);
    color: $color1;
  }

  .exList > li:nth-of-type(6) > p,
  .exList > li:nth-of-type(6) a {
    background-color: rgb(66, 134, 211);
  }

  .exList > li:nth-of-type(7) > p,
  .exList > li:nth-of-type(7) a {
    background-color: rgb(117, 66, 211);
  }

  .exList > li:nth-of-type(8) > p,
  .exList > li:nth-of-type(8) a {
    background-color: rgb(211, 66, 114);
  }
  .exList > li:nth-of-type(9) > p,
  .exList > li:nth-of-type(9) a {
    background-color: rgb(182, 66, 211);
  }

  .exList > li:nth-of-type(10) > p,
  .exList > li:nth-of-type(10) a {
    background-color: rgb(79, 102, 124);
  }
  .exList > li:nth-of-type(11) > p,
  .exList > li:nth-of-type(11) a {
    background-color: rgb(124, 79, 79);
  }

  .exContant {
    li {
      margin-left: 2rem;
      min-height: 4rem;
    }
  }
  .exContant > li > span,
