  .exContant > li:hover > p:nth-of-type(2) {
    background-color: rgb(68, 68, 68);
    color: #fff;
    display: block;
  }

  .exContant > li > p:nth-of-type(1)::before {
    content: "情境說明：";
  }
  .exContant > li > p:nth-of-type(2)::before {
    content: "使用說明：";
    font-size: 0.5rem;
  }
