        </li>
        <li>
          <h3>解決 vue-cli 3.x npm run build 打包後空白的問題?</h3>
          <ul>
            <h4>問題一，路徑問題?</h4>
            <li>
              <pre class="prettyprint">
//將 vue-router的模式更改為 hash 即可正常顯示內容
export default new Router({
- mode: history
+ mode: 'hash',
})</pre>
            </li>
            <li>
              <h4>問題二，配置文件問題?</h4>
              <a
                href="https://www.jianshu.com/p/25ad97547b89"
                target="_blank"
              >vue-cli 3.x打包訪問空白問題</a>
              <p>由於 vue-cli 3.x 版本去掉了配置文件，無法像2.x版本那樣把assetsPublicPath改成 './' 解決空白問題，所以正確的解決辦法，於跟目錄下創建配置文件</p>
              <pre class="prettyprint">
//目錄
根目錄
vue.config.js
// vue.config.js
module.exports={
  baseUrl:'./'
}</pre>
            </li>
          </ul>
        </li>
        <li>
          <h3>如何安裝 vue-cli3?</h3>
          <a
            href="https://www.youtube.com/watch?v=yBPpM8hez60"
            target="_blank"
          >vue-cli 3.x 初體驗</a>
          <ul>
            <li>
              <pre class="prettyprint">
//1.安裝前事先安裝好 node.js 8.10 以上版本、npm 最新版本
//2.輸入指令
$ npm install -g @vue/cli  //全域安裝 vue-cli 3.0
Vue –V //輸入檢查當前 vue 版本
vue create hello-world  //建立名稱為 hello-world 的 vue-cli專案
cd hello-world //移動到專案目錄
npm run serve  //本地啟動專案
npm run build  //打包專案</pre>
            </li>
          </ul>
        </li>
        <li>
